@import '../../../styles/scss_common/mixins';

.delivery-info{
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 60px;	
}

.delivery-info__item{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 15px;
}

.delivery-info__item-title{
	font-size: 32px;
	line-height: 130%;
	text-transform: capitalize;
	color: #222222;
	margin: 0;
}

.delivery-info__item-block{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 10px;
	z-index: 45;
}

.delivery-info__block-item{
	margin: 0;
	width: 100%;
	height: 60px;
	background-color: #FFFFFF;
	border: 1px solid #FCEDEA;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	padding-inline: 30px;
	font-size: 14px;
	line-height: 170%;
	color: #999999;
	position: relative;
	box-sizing: border-box;
	cursor: pointer;
	
}

.delivery-info__block-item span{color: #D05278; margin-left: 20px; font-size: 16px;}


.delivery-info__item-flex{
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	column-gap: 10px;
	width: 100%;
	height: 60px;	
}

.item-flex{width: 50%; flex-basis: 50%;}

.delivery-info__item-tip{
	width: 100%;
	background-color: #FCEDEA;
	height: 60px;
	display: flex;	
	flex-direction: row;
	justify-content: center;
	align-items: center;
	color: #666666;
	padding-inline: 20px;
	box-sizing: border-box;
	display: none;
	line-height: 140%;
	text-align: center;
}

.delivery-info__item:hover .delivery-info__item-tip{display: flex; transition: display 8s linear;}

.delivery-info__item-note{
	width: 100%;
	height: 100px;
	outline: none;
	border: 1px solid #FCEDEA;
	background-color: #FFFFFF;
	padding: 15px 30px;
	font-size: 14px;
	line-height: 170%;
	color: #999999;
	margin: 0;
	resize: none;
	overflow-y: auto;
	box-sizing: border-box;
}	

@media screen and (max-width: 4800px){
	.delivery-info{width: 50%; row-gap: 120px;}	
	.delivery-info__item{row-gap: 30px;}	
	.delivery-info__item-title{font-size: 60px; line-height: 130%;}	
	.delivery-info__item-block{row-gap: 20px;}	
	.delivery-info__block-item{height: 120px; border: 2px solid #FCEDEA; padding-inline: 30px; font-size: 36px;}	
	.delivery-info__block-item span{margin-left: 20px; font-size: 36px;}
	.delivery-info__item-flex{column-gap: 20px; height: 120px;}	
	.item-flex{width: 50%; flex-basis: 50%;}	
	.delivery-info__item-tip{height: 120px; font-size: 32px;}		
	.delivery-info__item-note{height: 200px; border: 2px solid #FCEDEA; padding: 15px 30px; font-size: 32px; line-height: 130%;}
}
