@import '../../../styles/scss_common/mixins';

.registration_form{
    width: 50vw;
    height: 700px;
    position: relative;
    background-color: #FAF9FF;
    border: 2px solid #EEEEEE;
    padding: 60px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
    z-index: 1;
    overflow: hidden;
}

.registration__form-decor{
    width: 894px;
    height: 820px;
    position: absolute;
    top: -21px;
    bottom: -21px;
    left: -62px;
    right: -62px;
    z-index: 10;
}

.registration__form-title{
    font-style: 40px;
    line-height: 140%;
    text-transform: capitalize;
    color: #222222;
    text-align: center;
    align-self: center;
}

.registration__form-block{
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    column-gap: 10px;
    z-index: 15;
}

.registration__block-input{
    width: 320px;
    height: 60px;
    border: none;
    outline: none;
    padding: 18px 30px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 170%;
    color: #999999;
    background-color: #FFFFFF;
    z-index: 15;
}

.registration__block-input:placeholder{
    font-size: 14px;
    line-height: 170%;
    color: #999999;
}

.registration__form-subtitle{
    margin: 0;
    font-size: 20px;
    line-height: 120%;
    text-transform: capitalize;
    color: #222222;
    margin-bottom: -20px;
    margin-top: 10px;
    margin-left: 10px;
}

.registration__form-remember{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}

.registration__remeber-status{
    width: 15px;
    height: 15px;
    border: 1px solid #EEEEEE;
    outline: none;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    z-index: 14;
}

.registration__remember-icon{
    width: 15px;
    height: 15px;
    z-index: 15;
}

.registratiom__remember-title{
    margin: 0;
    font-size: 16px;
    line-height: 170%;    
    color: #666666;
}

.registration__form-btn{
    width: 100%;
    height: 60px;
    background-color: #222222;
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 100%;    
    text-transform: uppercase;
    color: #FFFFFF;
    padding-block: 23px;
    z-index: 25;
}

.registration__form-login{
    font-size: 16px;
    line-height: 170%;   
    color: #666666;
    z-index: 20;
}

.login__link-red{
    color: #D05278;
    padding: 5px;
    display: inline-block;
    text-decoration: none;
    z-index: 20;
}

.registration__form-message{
    text-align: center;
    font-size: 14px;
    line-height: 100%;
    color: #D05278;
    margin-block: -20px;
}

@media screen and (max-width: 4800px){
    .registration_form{ width: 50vw; height: auto; border: 4px solid #EEEEEE; padding: 120px;  row-gap: 60px;}    
    .registration__form-decor{ width: 110%; height: 110%; top: -21px; bottom: -21px; left: -62px; right: -62px;}    
    .registration__form-title{font-size: 70px;}    
    .registration__form-block{ height: 120px; column-gap: 20px;}    
    .registration__block-input{ width: 23vw; height: 120px;  padding: 18px 30px; padding-left: 60px; font-size: 36px;}    
    .registration__block-input:placeholder{font-size: 36px;}    
    .registration__form-subtitle{font-size: 40px; margin-bottom: -20px;  margin-top: 10px; margin-left: 40px;}    
    .registration__form-remember{column-gap: 40px;}    
    .registration__remeber-status{ width: 60px; height: 60px;  border: 4px solid #EEEEEE; margin-left: 10px;}    
    .registration__remember-icon{ width: 60px; height: 60px;}    
    .registratiom__remember-title{font-size: 40px;}    
    .registration__form-btn{ width: 100%; height: 120px; font-size: 36px; padding-block: 23px;}    
    .registration__form-login{ font-size: 40px; margin-left: 10px;}    
    .login__link-red{ padding: 12px;}    
    .registration__form-message{ font-size: 40px; margin-block: -20px;}
}
