@import '../../../styles/scss_common/mixins';

.profile-info{
	width: 100vw;    
    position: relative;
    background-color: #FCEDEA;
    z-index: 1;
}

.profile-info__body{
	position: relative;
    width: 100vw;
    padding-bottom: 60px;   
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 50px;
    background-color: #FFFFFF;
}

.profile__info--decor{
	position: absolute;
    top: 0%;
    left: -18%;
    height: 500px;    
    opacity: 65%;
    z-index: 25;
}

.profile__info--decor2{
	position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-10deg);
    opacity: 65%;
    z-index: 25;
}

.profile__info-block{
	width: 80vw;
    padding-block: 120px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 60px;     
}

.profile__info-components{
	width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 65px;
}

.profile__info-components-gallery{
	align-self: flex-start;
    width: 100vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 5px;
	margin-bottom: -170px;
}

@media screen and (max-width: 4800px){
    .profile-info__body{ padding-bottom: 120px;  row-gap: 50px;}    
    .profile__info--decor{top: 30%; left: -11%; height: 900px; opacity: 65%;}    
    .profile__info--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}    
    .profile__info-block{ width: 80vw; padding-block: 180px; row-gap: 120px;}    
    .profile__info-components{ width: 100vw; row-gap: 130px;}     
    .profile__info-components-gallery{padding-top: 80px;} 
}
