@import '../../../styles/scss_common/mixins';

.shop__view-products{
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 15px;
    width: 100%;
    padding-top: 80px;
}
.shop__view-products--title{
    margin: 0;
    font-size: 24px;
    line-height: 100%;
    color: #222222;
    text-transform: capitalize;
    height: 41px;
    width: 100%;
    vertical-align: middle;
    border-bottom: 2px solid #222222;
    margin-bottom: 15px;
}
.shop__view-products--item{
    text-decoration: none;
    display: block;   
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
    border: 2px solid #FCEDEA;
    padding: 10px;    
}
.shop__view-products--image{    
    flex-basis: 30%;
    max-width: 27%; 
    object-fit: cover;     
}
.shop__view-products--info{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 10px;
}
.shop__view-products--info_title{
    font-size: 15px;
   line-height: 170%;
   color: #222222;
   text-transform: capitalize;
   margin: 0;
}
.shop__view-products--info_price{
    font-size: 14px;
    line-height: 170%;
    color: #999999;
    margin: 0;
}

@media screen and (max-width: 4800px){
    .shop__view-products{row-gap: 30px; padding-top: 80px;}    
    .shop__view-products--title{font-size: 46px; height: auto; border-bottom: 2px solid #222222; padding-bottom: 15px;}   
    .shop__view-products--item{ column-gap: 20px; border: 2px solid #FCEDEA; padding: 10px;}    
    .shop__view-products--image{ flex-basis: 30%; max-width: 27%; }   
    .shop__view-products--info{row-gap: 10px;}   
    .shop__view-products--info_title{font-size: 36px; line-height: 170%;}    
    .shop__view-products--info_price{ font-size: 36px; line-height: 170%;}
}
@media screen and (max-width: 3500px){    
    .shop__view-products--title{font-size: 40px;}   
    .shop__view-products--item{ column-gap: 30px;}    
    .shop__view-products--info_title{font-size: 32px;}    
    .shop__view-products--info_price{ font-size: 32px;}
}
@media screen and (max-width: 3000px){
    .shop__view-products{padding-top: 90px;}    
    .shop__view-products--title{font-size: 36px;}   
    .shop__view-products--item{ column-gap: 25px;}    
    .shop__view-products--info_title{font-size: 30px; width: 95%;}    
    .shop__view-products--info_price{ font-size: 30px;}
}
@media screen and (max-width: 2500px){   
    .shop__view-products--title{font-size: 32px; border-bottom: 1px solid #222222; padding-bottom: 10px;}   
    .shop__view-products--item{ column-gap: 20px;}    
    .shop__view-products--image{ flex-basis: 25%; max-width: 25%; }    
    .shop__view-products--info_title{font-size: 25px; line-height: 140%;}    
    .shop__view-products--info_price{ font-size: 25px; line-height: 140%;}
}
@media screen and (max-width: 2000px){
    .shop__view-products{row-gap: 20px; padding-top: 80px;}    
    .shop__view-products--title{font-size: 28px; padding-bottom: 15px;}   
    .shop__view-products--item{ column-gap: 20px;}     
    .shop__view-products--info_title{font-size: 22px;}    
    .shop__view-products--info_price{ font-size: 22px;}
}
@media screen and (max-width: 1560px){
    .shop__view-products{row-gap: 20px; padding-top: 60px;}    
    .shop__view-products--title{font-size: 24px; padding-bottom: 10px;} 
    .shop__view-products--image{ flex-basis: 27%; max-width: 27%; }     
    .shop__view-products--info_title{font-size: 17px;}    
    .shop__view-products--info_price{ font-size: 17px;}
}
@media screen and (max-width: 1260px){
    .shop__view-products{row-gap: 20px; padding-top: 80px;}    
    .shop__view-products--title{font-size: 22px; padding-bottom: 10px;} 
    .shop__view-products--image{ flex-basis: 27%; max-width: 27%; }     
    .shop__view-products--info_title{font-size: 16px;}    
    .shop__view-products--info_price{ font-size: 16px;}
}

@include adaptive(laptop){
    .shop__view-products{row-gap: 20px; padding-top: 80px;}    
    .shop__view-products--title{font-size: 20px;} 
    .shop__view-products--image{ flex-basis: 27%; max-width: 27%; }     
    .shop__view-products--info_title{font-size: 14px;}    
    .shop__view-products--info_price{ font-size: 15px;}
}
@include adaptive(tablet){
    .shop__view-products{row-gap: 16px; padding-top: 80px; width: 100%;}  
    .shop__view-products--item {padding: 7px;}    
    .shop__view-products--title{font-size: 18px; padding-bottom: 7px;} 
    .shop__view-products--image{ flex-basis: 25%; max-width: 25%; }     
    .shop__view-products--info_title{font-size: 13px; width: 95%;}    
    .shop__view-products--info_price{ font-size: 14px;}
}
@include adaptive(phone){
    .shop__view-products{row-gap: 16px; width: 100%; padding-top: 80px;}    
    .shop__view-products--title{font-size: 16px; padding-bottom: 7px;} 
    .shop__view-products--image{ flex-basis: 25%; max-width: 25%; }     
    .shop__view-products--info_title{font-size: 13px; width: 95%;}    
    .shop__view-products--info_price{ font-size: 14px;}
}