@import '../../../styles/scss_common/mixins';

.showroom-body{	
	padding-top: 120px;
	width: 100vw;	
	background-color: #FFFFFF;
	position: relative;
	z-index: 1;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	row-gap: 80px;
	cursor: pointer;
}

.showroom-body__decor{
	position: absolute;
	top: 8%;
	left: -7%;
	height: 430px;
	opacity: 70%;
	z-index: 5;
	
}

.showroom-body__decor-2{
	position: absolute;
	bottom: -3%;
	right: -1%;
	height: 400px;
	opacity: 70%;
	transform: rotate(75deg);
	z-index: 5;
}

.showroom__body-shop{	
    width: 70vw;
	margin: 0 auto;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 40px;    
    flex-wrap: wrap;
	z-index: 25;
    
}
.showroom__body-shop--card{
    flex-basis: 30%; 
    width: 600px;   
    height: 550px;    
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
    border: 3px solid #FCEDEA;    
    box-sizing: border-box;
    position: relative;
    text-decoration: none;
	z-index: 25;
}
.showroom__body-card--trackers{
    position: absolute;
    top: 0;
    right: 0;
    width: 100px;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 0;
	z-index: 25;

}

.showroom__body-trackers--sale, .showroom__body-trackers--new{
    width: 100px;
    height: 60px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 24px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.showroom__body-trackers--new{
    background-color: rgba(208, 82, 120, 0.7);
}

.showroom__body-card--image{
        height: 100%; 
		flex-basis: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
		z-index: 25;
        position: relative;
        padding-left: 10px;
}

.showroom__body-card--img{
	min-width: 3vw;	
    max-width: 20vw;
    min-height: 250px;
    max-height: 400px;	  
	z-index: 25; 
    object-fit: cover;
}

.showroom__body-card--info{
	flex-basis: 70%;
	width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 20px;    
    padding: 15px;
    padding-left: 0;
    padding-top: 110px;
	z-index: 25;
       
}

.showroom__body-info--title{   
    font-size: 24px;
    line-height: 130%;
    text-align: center;
    text-transform: capitalize;
    color: #222222;    
	width: 100%;
    margin: 0;
	z-index: 25;
}

.showroom-card-brand{
    color: #D05278;
    margin: 0;      
	z-index: 25;
}

.showroom__body-info--text{
	margin: 0;
	font-size: 17px;
	line-height: 150%;
	color: #666666;	
	text-align: center;
	margin-bottom: 20px;
    padding: 10px;
    box-sizing: border-box;
    height: 150px;
    overflow-y: auto;
}

.showroom__body-price--block{ 
    width: 100%;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 10px;
	z-index: 25;
}

.showroom__body-price--old{
    font-size: 20px;
    line-height: 120%;   
    text-decoration: line-through;
    color: #999999;   
    margin: 0;
	z-index: 25;
}

.showroom__body-price{
    font-size: 24px;
    line-height: 100%;    
    text-transform: capitalize;
    color: #666666;
    margin: 0;
	z-index: 25;
}

.showroom__block-pagination{
    margin: 0 auto;
    width: 310px;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 5px;
   
}

.showroom__pagination-item{
    box-sizing: border-box;
    background-color: #EEEEEE;
    border: 1px solid #EEEEEE;
    width: 40px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
}

.showroom__pagination-button{
    width: 40px;
    height: 100%;
    outline: none;
    border: none;
    background-color: transparent;
    color: #666666;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}


@media screen and (max-width: 4800px){
    .showroom-body{	padding-block: 180px; row-gap: 160px;}    
    .showroom-body__decor{top: 8%; left: -7%; height: 800px; opacity: 70%;}    
    .showroom-body__decor-2{bottom: -6%; right: -4%; height: 800px; opacity: 70%; transform: rotate(75deg);}       
    .showroom__body-shop{width: 80vw; gap: 80px; }
    .showroom__body-shop--card{flex-basis: 45%; width: 45%; height: 600px; column-gap: 40px; border: 3px solid #FCEDEA; padding: 40px;}
    .showroom__body-card--trackers{top: 0; right: 0; width: 123px; height: 120px;}    
    .showroom__body-trackers--sale, .showroom__body-trackers--new{ width: 123px; height: 60px; padding: 10px 25px; font-size: 28px;}
    .showroom__body-card--image{flex-basis: 30%; height: 600px;}    
    .showroom__body-card--img{min-height: 200px; max-height: 350px;  min-width: 3vw; max-width: 10vw;}   
    .showroom__body-card--info{ flex-basis: 70%; width: 70%; row-gap: 30px; padding: 0px; padding-top: 50px;}    
    .showroom__body-info--title{ font-size: 40px; line-height: 130%;}
    .showroom__body-info--text{font-size: 30px; line-height: 150%; margin-bottom: 0; padding: 0; width: 85%; height: 140px; overflow: hidden; text-overflow: ellipsis;
        display: -webkit-box; -webkit-line-clamp: 3; -webkit-box-orient: vertical;
    }   
    .showroom__body-price--block{ column-gap: 20px;}    
    .showroom__body-price--old{font-size: 40px; line-height: 120%; }    
    .showroom__body-price{font-size: 48px;}    
    .showroom__block-pagination{width: auto; height: 80px; column-gap: 10px;}    
    .showroom__pagination-item{width: 70px; height: 80px; font-size: 32px;}
    .showroom__pagination-button{width: 70px; font-size: 32px;}
}
