@import '../../../styles/scss_common/mixins';

.new-arrival{
    width: 100vw;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 180px;
    background-color: #FFFFFF;   
    z-index: 1;    
}

.new-collection{
    width: 100wv;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 56px;
}

.new-collection__title-block{
    width: 33vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 5px;
}

.new-collection__block-subtitle, .new-collection__body-subtitle{
    font-size: 50px;
    line-height: 100%;
    text-transform: capitalize;
    color: #D05278;
    margin: 0;
    z-index: 25;
}

.new-collection__block-title, .new-collection__body-title{
    font-size: 54px;
    line-height: 100%;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
    z-index: 25;    
}

.new-collection__block-text{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    text-align: center;
    margin: 0;
}

.new-collection__block-image{
    width: 100vw;
    height: 650px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 0;
}

.new-collection__image-item{
    flex-basis: 50%;
    width: 50vw;
    height: 650px; 
    background-color: #FCEDEA;   
}

.new-collection__item-image{
    width: 50vw;
    height: 650px;
    object-fit: cover;
}

.text-item{
    position: relative;   
    padding-left: 114px;
    padding-block: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    row-gap: 30px;
    box-sizing: border-box;
}

.new-collection__item-body{
    z-index: 25;
    align-self: flex-end;
    width: 35vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 30px;
    padding-block: 40px;

}
.new-collection__body-title{ font-size: 40px; margin-top: -20px; line-height: 120%;}
.new-collection__body-title span{color: #666666; font-size: 35px;}
.new-collection__body-description{
    font-size: 20px;
    line-height: 150%;
    color: #666666;
    width: 70%;
    margin: 0;
}
.new-collection__body-text{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    width: 75%;    
}

.new-collection__body-btn{
    width: 179px;
    height: 60px;
    background-color: #222222;
    outline: none;
    border: none;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}

.new-collection__btn-link{
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;    
}

.new-arrival__content{
    width: 468px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 30px;
    z-index: 20;
}

.new-arrival__content-logo{
    font-size: 50px;
    line-height: 100%;
    margin: 0;
    text-align: center;
    text-transform: capitalize;
    color: #D05278;
    z-index: 20;
    margin: 0;
}

.new-arrival__content-title{
    font-size: 54px;
    line-height: 100%;
    text-align: center;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
}

.new-arrival__content-text{
    font-size: 16px;
    line-height: 170%;
    text-align: center;
    color: #666666;
    margin: 0;
}

.new-arrival__cards{
    width: 94vw;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 20px;  
    overflow-x: hidden; 
    margin-top: -90px;
    align-self: center;
    
}

.new-arrival__cards_btn{
    width: 40px;
    height: 460px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 30;
    background-color: #fff;
}

.new-arrival__btns-btn{
    width: 40px;
    height: 60px;
    background-color: #FAF9FF;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 10px;  
    border: none;  
    outline: none;
    font-size: 22px;
    color: #999999;
}
.new-arrival__cards-block{ 
    background-color: #FFFFFF;    
    flex-basis: 95%;
    min-height: 460px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 20px;
    overflow: hidden; 
}

.new-arrival__cards-block::-webkit-scrollbar {
    width: 0;
}
.new-arrival__block-card{    
    width: 22%;
    flex-basis: 22%;
    min-height: 460px;   
    background-color: #f9f8f8; 
    display: flex;
    flex-direction: column;   
    justify-content: flex-start;
    align-items: center;
    padding-inline: 20px;
    padding-top: 10px;
    text-decoration: none;
    display: block;
}
.new-arrival__card-img{     
    min-height: 260px;
    position: relative; 
    display: flex;
    justify-content: center;
    align-items: center;   
}
.new-arrival__card-image{   
    height: 260px;  
    object-fit: cover;  
}
.new-arrival__img-new{
    position: absolute;
    top: -10px;
    right: -20px;
    width: 43px;
    height: 34px;
    background-color: rgba(208, 82, 120, 0.7);
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}
.new-arrival__img-sale{
    position: absolute;
    top: 44px;
    right: -20px;
    width: 43px;
    height: 34px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}
.new{
    background-color: rgba(208, 82, 120, 0.7);
    position: absolute;
    top: -10px;
    right: -20px;
    width: 43px;
    height: 34px;
}
.new-arrival__card-title{
    height: 70px;
    width: 85%;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
    margin-top: 30px;
    padding-inline: 40px;
}

.new-arrival__card-cost{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
    margin-top: 9px;
}

.new-arrival__card-old-price{
    font-size: 16px;
    line-height: 170%;
    text-align: center;
    text-decoration: line-through;
    color: #999999;
}
.new-arrival__card-price{
    font-size: 18px;
    line-height: 170%;
    text-align: center;
    color: #666666;
}

