@import '../../../styles/scss_common/mixins';

.login_form{
    width: 50vw;
    height: 630px;
    position: relative;
    background-color: #FAF9FF;
    border: 2px solid #EEEEEE;
    padding: 60px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 30px;
    z-index: 1;
    overflow: hidden;    

}

.login__form-decor{
    width: 785px;
    height: 705px;
    position: absolute;
    top: -37px;
    bottom: -37px;
    left: -67px;
    right: -67px;
    z-index: 10;
}

.login__form-title{
    font-style: 40px;
    line-height: 140%;
    text-transform: capitalize;
    color: #222222;
    align-self: center;
}

.login__input{
    width: 450px;
    height: 60px;
    border: none;
    outline: none;
    padding: 18px 30px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 170%;
    color: #999999;
    background-color: #FFFFFF;
    z-index: 15;
}

.login__input:placeholder{
    font-size: 14px;
    line-height: 170%;
    color: #999999;
}
.login__form-remember{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}

.login__remeber-status{
    width: 15px;
    height: 15px;
    border: 1px solid #EEEEEE;
    outline: none;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    z-index: 14;
}

.login__remember-icon{
    width: 15px;
    height: 15px;
    z-index: 15;
}

.login__remember-title{
    margin: 0;
    font-size: 16px;
    line-height: 170%;    
    color: #666666;
}

.login__form-btn{
    width: 100%;
    height: 60px;
    background-color: #222222;
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 100%;    
    text-transform: uppercase;
    color: #FFFFFF;
    padding-block: 23px;
    z-index: 25;
}

.login__form-register{
    font-size: 16px;
    line-height: 170%;   
    color: #666666;
    z-index: 20;
}

.register__link-red{
    color: #D05278;
    padding: 5px;
    display: inline-block;
    text-decoration: none;
    z-index: 20;
}

.login__form-message{
    text-align: center;
    font-size: 14px;
    line-height: 100%;
    color: #D05278;
    margin-block: -20px;
}

@media screen and (max-width: 4800px){
    .login_form{width: 40vw; height: auto; border: 4px solid #EEEEEE; padding: 120px; row-gap: 60px;}    
    .login__form-decor{width: 110%; height: 110%; top: -21px; bottom: -21px; left: -62px; right: -62px;}    
    .login__form-title{font-size: 70px;}    
    .login__input{width: 30vw; height: 120px; padding: 18px 30px; padding-left: 60px; font-size: 36px; text-align: center;}    
    .login__input::placeholder{font-size: 36px;}
    .login__form-remember{column-gap: 40px;}    
    .login__remeber-status{width: 60px; height: 60px; border: 4px solid #EEEEEE; margin-left: 10px;}    
    .login__remember-icon{ width: 60px; height: 60px;}    
    .login__remember-title{font-size: 40px;}    
    .login__form-btn{width:30vw; height: 120px;  font-size: 36px; padding-block: 23px;}    
    .login__form-register{font-size: 40px; margin-left: 10px;}    
    .register__link-red{padding: 12px;}
    .login__form-message{font-size: 40px;}
}
