@import '../../styles/scss_common/mixins';

.header-intro{
    position: relative;
    width: 100vw;
    min-height: 300px;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #FCEDEA;
    z-index: 11;
}

.header-intro_bg-decor{
    position: absolute;
    left: -5%;
    top: 10%;    
    width: 250px;    
    opacity: 75%;
    z-index: 50;
}

.second-left{
    left: 110px;
    top: 20%;   
    z-index: 50; 
}


.left-right{
    position: absolute;
    right: 19%;
    top: 17%;
    width: 250px;    
    transform: rotate(-60deg);
    opacity: 75%;
    z-index: 50;
}

.right{
    position: absolute;
    right: 20%;
    top: -17%;
    width: 250px;    
    transform: rotate(30deg);
    opacity: 75%;
    z-index: 50;
    
}

.header-intro-content{
    min-width: 682px;    
    padding-block: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;
    margin-left: -80px;
}

.header-intro__content-title{
    font-size: 70px;
    line-height: 100%;
    text-align: center;
    color:#222222;
    text-transform: capitalize;
    margin: 0;   
}
.header-intro__content-tracker{
    margin: 0;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
}

#tracker-red{
    color: #D05278;
    font-size: 16px;
    line-height: 170%;
    text-transform: capitalize;
}

@media screen and (max-width: 4800px) {
    .header-intro{min-height: 400px;}
    .header-intro_bg-decor{ left: -1%;  width: 350px;}
    .second-left{left: 260px; top: 24%;}
    .left-right{right: 33%; width: 350px; top: 7%; z-index: 100;}
    .right{right: 34%; top: -47%; width: 350px; z-index: 100; }
    .header-intro__content-tracker{font-size: 38px;}
    #tracker-red{font-size: 38px;}
    .header-intro-content{margin-left: -350px;} 
    .header-intro__content-title{font-size: 100px;}   
}
@media screen and (max-width: 3500px){
    .header-intro__content-title{font-size: 80px;}
    .header-intro{min-height: 370px;}
    .left-right{right: 33%; width: 350px; top: 3%; z-index: 100;}
    .right{right: 34%; top: -57%; width: 350px; z-index: 100; }
}
@media screen and (max-width: 3000px){
    .header-intro_bg-decor{ left: -1%;  width: 350px;}
    .second-left{left: 260px; top: 24%;}
    .left-right{right: 31%; width: 350px; top: 6%; z-index: 100;}
    .right{right: 32%; top: -51%; width: 350px; z-index: 100; }
}
@media screen and (max-width: 2800px){    
    .header-intro_bg-decor{ left: 0%;  width: 350px;}
    .second-left{left: 220px; top: 24%;}
    .left-right{right: 27%; width: 350px; top: 6%;}
    .right{right: 28%; top: -51%; width: 350px;}
    .header-intro__content-tracker{font-size: 34px;}
    #tracker-red{font-size: 34px;}
    .header-intro__content-title{font-size: 70px;}
}
@media screen and (max-width: 2500px){
    .header-intro_bg-decor{ left: -2%;  width: 300px;}
    .second-left{left: 200px; top: 24%;}
    .left-right{right: 23%; width: 300px; top: 12%;}
    .right{right: 24%; top: -38%; width: 300px;}
}
@media screen and (max-width: 2280px){
    .header-intro_bg-decor{ left: -2%;  width: 300px;}
    .second-left{left: 200px; top: 24%;}
    .left-right{right: 20%; width: 300px; top: 13%;}
    .right{right: 21%; top: -36%; width: 300px;}
}
@media screen and (max-width: 2150px) {     
    .header-intro-content{margin-left: -250px;}    
}
@media screen and (max-width: 2000px) {
    .header-intro_bg-decor{top: 18%; left: -1%;  width: 250px;}
    .second-left{left: 170px; top: 30%;}      
    .left-right{right: 18%; width: 280px; top: 23%;}
    .right{right: 19%; top: -23%; width: 280px; transform: rotate(50deg);}
    .header-intro__content-tracker{font-size: 27px;}
    #tracker-red{font-size: 27px;}
    .header-intro-content{margin-left: -110px; row-gap: 15px;}   
}
@media screen and (max-width: 1830px) {
    .header-intro{min-height: 325px;}
    .header-intro_bg-decor{top: 18%; left: -1%;  width: 250px;}
    .second-left{left: 170px; top: 30%;}   
    .left-right{right: 18%; width: 250px;}
    .right{right: 19%; top: -21%; width: 250px;  transform: rotate(40deg);}    
    .header-intro-content{margin-left: -80px;}
    .header-intro__content-title{font-size: 60px;}
    .header-intro__content-tracker{font-size: 26px;}
    #tracker-red{font-size: 26px;}
}
@media screen and (max-width: 1560px) {
    .header-intro{min-height: 290px;}
    .header-intro_bg-decor{ left: -2%; top: 18%;  width: 210px;}
    .second-left{left: 130px; top: 30%; width: 210px;}  
    .left-right{right: 20%; top: 25%; width: 210px;}
    .right{right: 19%; top: -22%; width: 210px;}
    .header-intro__content-title{font-size: 55px;}
    .header-intro__content-tracker{font-size: 22px;}
    #tracker-red{font-size: 22px;}    
}
@media screen and (max-width: 1440px) {    
    .header-intro_bg-decor{ left: -2%; top: 18%;  width: 200px;}
    .second-left{left: 130px; top: 30%; width: 200px;}  
    .left-right{right: 20%; top: 25%; width: 200px;}
    .right{right: 19%; top: -22%; width: 200px;}
    .header-intro__content-title{font-size: 50px;}
    .header-intro__content-tracker{font-size: 22px;}
    #tracker-red{font-size: 22px;}    
}

@media screen and (max-width: 1300px) {
    .left-right{display: none;;}
    .right{display: none;}
    .second-left{transform: rotate(6deg); top: 33%; left: 130px;}
    .header-intro-content{margin-left: 0;}       
}

@media screen and (max-width: 1200px){
    .header-intro{min-height: 260px;}
    .header-intro_bg-decor{ left: -1%; top: 24%;  width: 170px;}
    .second-left{left: 127px; top: 34%; width: 170px;}
    .header-intro-content{row-gap: 12px; min-width: 300px;}
    .header-intro__content-title{font-size: 45px;}
    .header-intro__content-tracker{font-size: 20px;}
    #tracker-red{font-size: 20px;} 
}

@include adaptive(laptop){
    .header-intro{min-height: 250px;}
    .left-right{display: none;}
    .right{display: none;}
    .second-left{display: none;}
    .header-intro_bg-decor{display: none;}
    .header-intro__content-title{font-size: 40px}
    .header-intro__content-tracker{font-size: 18px;}
    #tracker-red{font-size: 18px;}
}
@include adaptive(tablet){
    .header-intro-content{row-gap: 12px; padding-top: 40px;}
    .header-intro__content-title{font-size: 40px;}
    .header-intro__content-tracker{font-size: 17px;}
    #tracker-red{font-size: 17px;}
}
@include adaptive(phone){
    .header-intro-content{row-gap: 10px; padding-top: 30px;}
    .header-intro__content-title{font-size: 36px;}
    .header-intro__content-tracker{font-size: 16px;}
    #tracker-red{font-size: 16px;}
}

@media screen and (max-width: 370px){
    .header-intro__content-title{font-size: 34px;}
    .header-intro__content-tracker{font-size: 16px;}
    #tracker-red{font-size: 16px;}
}
