@import '../../styles/scss_common/mixins';

.cart{
    width: 100vw;
    height: 100%;
    position: relative;  
    background-color: #FCEDEA; 
    z-index: 1;
   
}

.cart__card-decor{
    position: absolute;
    top: 30%;
    left: -22%;
    height: 600px;    
    opacity: 65%;
    z-index: 25;
}
.cart__card-decor2{
    position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-30deg);
    opacity: 65%;
    z-index: 25;
}

.cart__content{
    width: 100vw; 
    min-height: 400px;   
    background-color: #fff;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 60px;
    padding-block: 180px; 
    z-index: 15;
}

.cart__content-block{
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
    position: relative;
}
.cart__block-head{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 50px;
    padding-bottom: 5px;
    border-bottom: 2px solid #222222;
    z-index: 15;
    
}

.cart__head-item{
    margin: 0;
    font-size: 20px;
    line-height: 120%;
    text-transform: capitalize;
    color: #222222;   
}

.item-price{padding-left: -5px;}
.item-product, .cart__item-product{flex-basis: 40%;}
.item-price, .cart__item-price{flex-basis: 12%;}
.item-quantity, .cart__item-quantity{ flex-basis: 20%;}
.item-totals, .cart__item-amount{flex-basis: 15%;}


.cart__block-body{
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;
    color: #222222;
    font-size: 16px;
    line-height: 120%;    
}

.cart__block-body--message{
    font-size: 18px;
    color: #D05278;
}

.cart__body-item{
    width: 80vw;
    height: 170px;
    background-color: #FFFFFF;
    border: 2px solid #EEEEEE;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: center;
    column-gap: 70px;
    box-sizing: border-box;
    position: relative;
}
.cart__item-product{        
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 30px;
}

.cart__product-image{
    min-height: 80px;
    max-height: 150px;
    min-width: 3vw;
    max-width: 15vw;
    object-fit: cover;
    flex-basis: 30%;
}
.cart__product-info{    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 5px;
    flex-basis: 70%;
}

.cart__product-info--title{
    font-size: 24px;
    line-height: 100%;
    color: #000000;
    text-transform: capitalize;
    margin: 0;
    margin-bottom: 10px;
}

.cart__product-info--stock{
    margin: 0;
    font-size: 12px;
    line-height: 120%;
    text-transform: uppercase;
    color: #12A33B;
}

.cart__product-info--sku{
    margin: 0;
    font-size: 12px;
    line-height: 170%;    
    color: #666666;
}

.cart__item-price{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 10px;
}    

.cart__price-old{
    font-size: 14px;
    line-height: 100%;
    text-decoration: line-through;
    text-transform: capitalize;
    color: #999999;
    margin: 0;
}
.cart__price-new{
    font-size: 18px;
    line-height: 100%;    
    text-transform: capitalize;
    color: #666666;
    margin: 0;
}

.cart__item-quantity{    
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}

.cart__item-change--btn{
    width: 40px;
    height: 40px;
    background-color: #EEEEEE;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
    color: #999999;
}

.cart__item-change--indicator{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 170%;
    color: #222222;
}

.cart__item-amount{
    height: 100%;
    display: flex;
    flex-direction: column;    
    justify-content: center;
    align-items: flex-start;
    line-height: 150%;    
    color: #222222;
    font-size: 20px;

}

