@import '../../../styles/scss_common/mixins';

.about__success{
    width: 100vw;
    height: 700px;
    position: relative;
    z-index: 1;
    box-sizing: border-box;   
    background-color: #FCEDEA; 
    display: flex;
    flex-direction: row;  
    justify-content: center;
    align-items: center;
}

.about__success-woman{
    height: 700px;    
    z-index: 15;
    position: absolute;
    bottom: 0%;
    left: -17%; 
    opacity: 90%;   
}
.about__success-decor{
    position: absolute;
    bottom: -6%;
    right: -8%;
    height: 400px; 
    opacity: 70%;   
    z-index: 15;
    transform: rotate(5deg);
}

.about__success-content{
    width: 40vw;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
}

.about__success-content--logo{
    font-size: 50px;
    line-height: 100%;
    color: #D05278;
    text-transform: capitalize;
    margin: 0;
}

.about__success-content--title{
    font-size: 54px;
    line-height: 100%;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
    margin-top: -30px;
}
.about__success-content--subtitle{
    font-size: 20px;
    line-height: 150%;    
    color: #666666;
    margin: 0;
    margin-top: -6px;
}

.about__success-content--text{
    font-size: 16px;
    line-height: 170%;    
    color: #666666;
    margin: 0;
}
.about__success-content--btn{
    width: 179px;
    height: 60px;
    background-color: #222222;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 23px 30px; 
    box-sizing: border-box;  
    border: none;
    outline: none;
}

.about__success-btn-link{
    text-decoration: none;
    font-size: 14px;
    line-height: 100%;
    color: #FFFFFF;
    text-transform: uppercase;
    z-index: 25;
}

@media screen and (max-width: 4800px) {
    .about__success{height: 800px;}
    .about__success-woman{bottom: 0%;left: -2%; height: 800px; }
    .about__success-decor{bottom: -12%; right: -5%; height: 800px; }
    .about__success-content{width: 28vw;  row-gap: 40px;}    
    .about__success-content--logo{ font-size: 60px;}    
    .about__success-content--title{font-size: 60px; margin-top: -30px;}
    .about__success-content--subtitle{font-size: 28px;margin-top: -6px; width: 75%;}
    .about__success-content--text{font-size: 24px; width: 75%;}
    .about__success-content--btn{width: 15vw; height: 80px; padding: 23px 30px; }    
    .about__success-btn-link{font-size: 26px;}
}
@media screen and (max-width: 3000px){
    .about__success-woman{left: -8%;}
    .about__success-content{width: 33vw; margin-left: 250px;} 
    .about__success-decor{right: -7%; height: 700px; }      
    .about__success-content--title{font-size: 50px; width: 75%;}  
    .about__success-content--subtitle{font-size: 24px; width: 75%;}
    .about__success-content--text{font-size: 22px;}
    .about__success-btn-link{font-size: 22px;}     
}
@media screen and (max-width: 2500px){
    .about__success{height: 700px;}
    .about__success-woman{bottom: 0%; left: -10%; height: 700px; }
    .about__success-decor{position: absolute; bottom: -12%; right: -10%; height: 600px; }     
    .about__success-content{width: 40vw;  row-gap: 40px; margin-left: 650px;}
    .about__success-content--logo{ font-size: 60px;}    
    .about__success-content--title{font-size: 50px; margin-top: -30px; width: 65%; line-height: 120%;}  
    .about__success-content--subtitle{font-size: 24px; margin-top: -6px; width: 80%;}
    .about__success-content--text{font-size: 22px; width: 80%;}
    .about__success-btn-link{font-size: 22px;}
     
}
@media screen and (max-width: 2300px){.about__success-content{width: 50vw;}}
@media screen and (max-width: 2000px){
    .about__success{height: 700px;}
    .about__success-woman{left: -16%;}
    .about__success-decor{ right: -12%;}     
    .about__success-content{width: 55vw; margin-left: 650px;}      
    .about__success-content--title{width: 75%;}  
    .about__success-content--subtitle{font-size: 22px; width: 70%;}
    .about__success-content--text{font-size: 20px; width: 70%;}
    .about__success-btn-link{font-size: 20px;}     
}
@media screen and (max-width: 1700px){
    .about__success{justify-content: flex-end;}
    .about__success-woman{bottom: 0%; left: -19%; height: 700px; }
    .about__success-decor{display: none; }     
    .about__success-content{width: 50vw;  row-gap: 40px; margin-left: 0px;}
    .about__success-content--logo{ font-size: 60px;}    
    .about__success-content--title{font-size: 45px; margin-top: -30px; width: 85%;}  
