    .about__success-content--text{font-size: 20px; width: 90%;}
    .about__success-content--btn{width: 20vw; height: 80px; padding: 23px 30px; }   
    .about__success-btn-link{font-size: 20px;}     
}
@media screen and (max-width: 1440px){    
    .about__success-woman{bottom: 0%; left: -25%; height: 700px; }       
    .about__success-content{width: 45vw;  row-gap: 30px; margin-left: 0px;}
    .about__success-content--logo{ font-size: 50px;}    
    .about__success-content--title{font-size: 40px; margin-top: -30px; width: 85%;}  
    .about__success-content--subtitle{font-size: 20px;margin-top: -6px; width: 90%;}
    .about__success-content--text{font-size: 18px; width: 90%;}
    .about__success-btn-link{font-size: 20px;}     
}
@media screen and (max-width: 1200px){
    .about__success-woman{bottom: 0%; left: -37%;} 
    .about__success-content--title{font-size: 30px; margin-top: -10px; width: 85%;}
    .about__success-content--subtitle{font-size: 18px;}
    .about__success-content--text{font-size: 14px;}
    .about__success-content--btn{width: 20vw; height: 70px; padding: 13px 30px; }   
}
@include adaptive(laptop){    
    .about__success-woman{bottom: 0%; left: -45%; opacity: 30%;} 
    .about__success-content{width: 50vw;  row-gap: 20px; margin-left: 0px; padding-top: 0;}
    .about__success-content--title{font-size: 30px; margin-top: -10px; width: 85%;}
    .about__success-content--subtitle{font-size: 16px;}
    .about__success-content--text{font-size: 12px;}
    .about__success-content--btn{width: 20vw; height: 70px; padding: 13px 30px; }   
