@media screen and (max-width: 650px){
    .about__success-woman{bottom: 0%; left: -82%;} 
}
@media screen and (max-width: 600px){
    .about__success-woman{bottom: 0%; left: -90%;} 
}
@include adaptive(phone){
    .about__success{align-items: center; padding-top: 0; height: 600px;}
    .about__success-content{width: 80vw;  row-gap: 60px; margin-left: 0px; padding-top: 0px; }
    .about__success-content--btn{width: 50vw;}   
    .about__success-woman{opacity: 15%; height: 600px; left: -55%;} 
    .about__success-content--logo{ font-size: 50px;}    
