    .about__success-content--subtitle{font-size: 16px;}    
    .about__success-content--btn{width: 60vw;}  
}
@media screen and (max-width: 400px){
    .about__success{align-items: flex-start; overflow: hidden;}
    .about__success-woman{bottom: 0%;  left: -98%;}     
    .about__success-content{row-gap: 60px; padding-top: 60px;}    
    .about__success-content--btn{width: 60vw; height: 50px;}   
}


