@import '../../../styles/scss_common/mixins';

.showroom{
    width: 100vw;
	background-color: #FCEDEA;
    position: relative;
    z-index: 1;       
}

.showroom__intro{
    position: relative;
    width: 100vw;    
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 20px;
}

.showroom_bg-decor{
    position: absolute;
    left: -5%;
    top: 10%;
    z-index: 11;
    width: 250px;
    transform: rotate(-0deg);
    opacity: 75%;
}

.showroom-second-left{
    left: 110px;
    top: 20%;
    transform: rotate(0deg);
}

.showroom-left-right{
    position: absolute;
    right: 19%;
    top: 17%;
    width: 250px;
    z-index: 11;
    transform: rotate(-90deg);
    opacity: 75%;
}

.showroom-right{
    position: absolute;
    right: 20%;
    top: -17%;
    width: 250px;
    z-index: 11;
    transform: rotate(30deg);
    opacity: 75%;
    
}

.showroom__intro-content{
    width: 682px;    
    padding-block: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;


}

.showroom__content-title{
    font-size: 70px;
    line-height: 100%;
    text-align: center;
    color:#222222;
    text-transform: capitalize;
    margin: 0;
}

.showroom__content-tracker{
    margin: 0;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
}

.tracker-red-style{
    color: #D05278;
    font-size: 16px;
    line-height: 170%;    
}

.showroom__content-info{width: 85%; text-align: center; margin: 0; margin-top: 20px; }


@media screen and (max-width: 4800px){
    .showroom__intro{padding-bottom: 50px; min-height: 550px;}    
    .showroom_bg-decor{left: -1%; top: 15%; width: 420px;}    
    .showroom-second-left{left: 290px; top: 25%;}    
    .showroom-left-right{right: 27%; width: 420px; top: 24%; z-index: 100; transform: rotate(-70deg);}
    .showroom-right{right: 28%; top: -26%; width: 420px; z-index: 100; } 
    .showroom__intro-content{width: 30vw; padding-block: 50px; row-gap: 20px; margin-left: -350px;}    
    .showroom__content-title{ font-size: 100px;}    
    .showroom__content-tracker{font-size: 38px;}    
    .tracker-red-style{font-size: 38px;}    
    .showroom__content-info{width: 85%; text-align: center; margin: 0; margin-top: 20px; font-size: 28px; color: #666666; line-height: 160%;}
}
