@import '../../styles/scss_common/mixins';

.leave-comment{
	width: 410px;	
	height: 702px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	row-gap: 60px;
	overflow: hidden;
	background-color: #FCEDEA;
	cursor: pointer;
	position: relative;
	padding: 65px;
	box-sizing: border-box;
}

.leave-comment__block-item{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	row-gap: 5px;
}

.leave-comment__item-title{
	font-size: 32px;
	line-height: 130%;
	text-transform: capitalize;
	color: #222222;
	margin: 0;
}  

.leave-comment__item-text{
	margin: 0;
	font-size: 14px;
	line-height: 170%;
	color: #666666;
	text-align: center;

}

.leave-comment__rating-block{
	width: 50%;
	margin-left: -100px;
}

.leave-comment__block-form{
	position: relative;
	width: 290px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	row-gap: 10px;
	z-index: 5;	
}


.leave-comment__form-input{
	outline: none;
	border: none;
	background-color: #FFFFFF;
	width: 290px;
	height: 60px;
	padding: 18px 30px;
	font-size: 14px;
	line-height: 170%;
	color: #999999;
	box-sizing: border-box;
	border: 1px solid #FCEDEA;
}

leave-comment__form-input::placeholder{
	font-size: 14px;
	line-height: 170%;
	color: #999999;
}

.input-review{
	width: 290px;
	height: 150px;
	overflow-y: auto;
}

.leave-comment__item-title::-webkit-scrollbar {
    width: 12px;               /* ширина scrollbar */
}
.leave-comment__item-title::-webkit-scrollbar-track {
    background: #FCEDEA;        /* цвет дорожки */
}
  
.leave-comment__item-title::-webkit-scrollbar-thumb {
    background-color: #FCEDEA;    /* цвет плашки */
    border-radius: 20px;       /* закругления плашки */
    border: 3px solid #999999;  /* padding вокруг плашки */
}

.leave-comment__form-btn{
	width: 138px;
	height: 60px;
	background-color: #222222;
	padding: 23px 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	line-height: 100%;
	text-transform: uppercase;
	color: #FFFFFF;
	box-sizing: border-box;
	outline: none;
	border: none;
}

.leave-comment__icon{
	position: absolute;
	bottom: 0px;
	right: 0px;
	width: 249px;
	height: 214px;		
	z-index: 1;
}

.leave-comment__image{
	position: absolute;
	bottom: -12%;
	right: -10%;
	width: 335px;
	height: 289px;
	z-index: 4;
}

.leave-comment__form-message{
	font-size: 16px;
	line-height: 170%;
	color: #D05278;
	margin-left: 20px;
}


