@import '../../../styles/scss_common/mixins';

.product{
    width: 100vw;   
    position: relative;    
    background-color: #FCEDEA;    
    z-index: 1;   
}

.product__block-navigator{
    position: fixed;
    right: -1%;
    top: 510px;
    background-color: #999999;
    border-radius: 20px 0 0 20px;
    padding: 10px;    
    padding-right: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 10px;
    z-index: 45;
}

.product__block-navigator--item{
    text-decoration: none;    
    width: 159px;
    height: 50px;
    background-color: #FCEDEA;
    color: #999999;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 45;
}

.product__intro{
    position: relative;
    width: 100vw;
    min-height: 300px;
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.product_bg-decor{
    position: absolute;
    left: -5%;
    top: 10%;
    z-index: 11;
    width: 250px;
    transform: rotate(-0deg);
    opacity: 75%;
}

.second-left{
    left: 110px;
    top: 20%;
    transform: rotate(0deg);
}

.left-right{
    position: absolute;
    right: 19%;
    top: 17%;
    width: 250px;
    z-index: 11;
    transform: rotate(-40deg);
    opacity: 75%;
}

.right{
    position: absolute;
    right: 20%;
    top: -17%;
    width: 250px;
    z-index: 11;
    transform: rotate(30deg);
    opacity: 75%;    
}

.product__intro-content{    
    width: 682px;    
    padding-top: 50px; 
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;
}

.product__content-title{
    font-size: 70px;
    line-height: 100%;
    text-align: center;
    color:#222222;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
}
.product__content-tracker, .shop__item-tracker-red{
    margin: 0;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    text-transform: capitalize;
    text-align: center;
}

.shop__item-tracker-red{
    color: #D05278;
    text-transform: capitalize;
}

.product__card{
    width: 100vw;
    height: auto;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 60px;
    background-color: #FFFFFF;
    padding-block: 80px;   
}

.product__card-decor{
    position: absolute;
    top: 15%;
    left: -22%;
    height: 600px;    
    opacity: 65%;
    z-index: 25;
}
.product__card-decor2{
    position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-30deg);
    opacity: 65%;
    z-index: 25;
}

.product__card-block{
    width: 70vw;   
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 60px;  
    padding: 30px;      
}


.product__block-image--block{
    width: 35vw;   
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    position: relative;    
    background-color: #FAF9FF;
}
.product__block-image{
    width: 450px;   
    align-self: center;
    object-fit: cover;   
}

.product__block-trackers{
    position: absolute;
    top: 0;
    right: 0;
    width: 83px;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 0;

}

.product__block-trackers--sale, .product__block-trackers--new{
    width: 83px;
    height: 40px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.product__block-trackers--new{
    background-color: rgba(208, 82, 120, 0.7);
}

.product__block-info{      
     width: 50%;
     height: 100%;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     align-items: flex-start;
     row-gap: 30px;     
     box-sizing: border-box;
}

.product__block-info--title{
    font-size: 40px;
    line-height: 140%;
    text-transform: capitalize;
    color: #000000;
    margin: 0;
}

.product__block-info--stock{
    font-size: 16px;
    line-height: 170%;
    text-transform: uppercase;
    color: #12A33B;
    margin: 0;
}

.product__block-info--price{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}


.product__block-price--old{
    font-size: 20px;
    line-height: 100%;   
    text-decoration: line-through;
    text-transform: capitalize;
    color: #999999;   
    margin: 0;
}

.product__block-price--new{
    font-size: 32px;
    line-height: 130%;    
    text-transform: capitalize;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
}

.product__block-info--descr_block{
    width: 100%;    
    padding-bottom: 30px;
}

.product__block-info--description{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    width: 70%;
    margin: 0;
}

.product__block-order--quatity{    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 10px;
    
}

.product__block-quantity--title{    
    font-size: 16px;
    line-height: 170%;
    color: #222222;
    margin: 0;
    margin-left: 30px;
}

.product__block-quantity-change{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 25px;
    width: 140px;
}

.product__block-change--btn{
    width: 40px;
    height: 40px;
    background-color: #EEEEEE;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
    color: #999999;
}

.product__block-change--indicator{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 170%;
    color: #222222;
}

.product__block-btns{
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 11px;
}

.product__block-btns--btn{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
    width: 163px;
    height: 60px;
    border: none;
    outline: none;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
}

.wish-image{
    width: 25px;
    height: 22.5px;
}


.cart-btn{
    background-color: #222222;
    color: #FFFFFF;
    text-decoration: none;
}

.wish{
    background-color: #FAF9FF;
    border: 1px solid #EEEEEE;
    color: #222222;
    text-decoration: none;
}

.product__order-message{
    font-size: 16px;
    line-height: 170%;
    color: #222222;
    margin: 0;
    text-align: center;
}

