@media screen and (max-width: 1400px){ .product__card-decor{top: 15%; left: -30%; height: 700px;} }
@media screen and (max-width: 1300px){
    .left-right{display: none;;}
    .right{display: none;}
    .second-left{transform: rotate(6deg); top: 33%; left: 130px;}
    .product__intro-content{margin-left: 0;}    
    .product__block-navigator{top: 380px; position: absolute;}    
    .product__block-navigator--item{ width: 17vw; height: 60px; font-size: 17px; padding: 5px 10px; text-align: center;}     

    .product__card{padding-block: 100px;}
    .product__card-block{width: 85vw; column-gap: 70px;}  
    .product__card-decor{top: 15%; left: -31%; height: 600px;} 
    .product__card-decor2{height: 500px; right: -19%;} 
    .product__block-image--block{width: 40vw; min-height: 630px;}
    .product__block-image{min-width: 10vw; max-width: 27vw; max-height: 560px; min-height: 300px;}
    .product__block-trackers{width: 103px; height: 100px;}    
    .product__block-trackers--sale, .product__block-trackers--new{width: 103px; height: 50px; padding: 5px 15px; font-size: 20px;} 
    .product__block-info{width: 40vw; row-gap: 20px;}
    .product__block-info--title{ font-size: 22px; width: 75%;}   
    .product__block-info--stock{ font-size: 18px;}  
    .product__block-info--descr_block{width: 75%; height: 200px;}
    .product__block-info--description{ font-size: 17px; width: 100%;} 
    .product__block-info--price{column-gap: 20px;}    
    .product__block-price--old{font-size: 16px;} 
    .product__block-price--new{font-size: 28px;} 
    .product__block-order--quatity{row-gap: 20px;} 
    .product__block-quantity--title{ font-size: 20px; margin-left: 30px;}
    .product__block-change--btn{ width: 40px; height: 40px; font-size: 18px;}    
    .product__block-change--indicator{font-size: 20px;}      
    .product__block-btns{ padding-top: 20px; column-gap: 15px;}    
    .product__block-btns--btn{column-gap: 5px; width: 15vw; height: 60px; font-size: 18px; }  
    .product__block-btn-image {width: 20px; height: 25px; margin-right: 5px;}
    .wish-image{ width: 30px; height: 25px; margin-right: 5px;}  
    .product__order-message{font-size: 14px;}
}
@media screen and (max-width: 1150px){
     .product__card-decor{top: 15%; left: -34%; height: 600px;}
     .product__content-tracker{line-height: 170%;} 
     .product__content-tracker, .shop__item-tracker-red{font-size: 16px;}  
     .product__card-decor2{height: 400px; right: -17%;} 

    .product__block-image--block{width: 37vw; min-height: 600px;}
    .product__block-image{min-width: 10vw; max-width: 30vw; max-height: 450px; min-height: 250px;}
    .product__block-info{width: 40vw; row-gap: 20px;}
    .product__block-info--descr_block{width: 73%; height: 200px;}    
}
@include adaptive(laptop){    
    .product_bg-decor{display: none;}
    .left-right{display: none;}
    .right{display: none;}
    .second-left{display: none;} 
    .product__content-tracker{line-height: 200%;}  
    .product__content-tracker, .shop__item-tracker-red{font-size: 16px;}  
    .product__content-title{font-size: 50px;}  
    .product__block-navigator{top: 380px; position: absolute;}    
    .product__block-navigator--item{ width: 19vw; height: 60px; font-size: 16px; padding: 5px 10px; text-align: center;}   

    .product__card{padding-block: 100px;}
    .product__card-decor{display: none;} 
    .product__card-decor2{display: none;} 
    .product__card-block{width: 90vw; column-gap: 70px;}  
    .product__block-image--block{width: 43vw; min-height: 530px;}
    .product__block-image{min-width: 10vw; max-width: 33vw; max-height: 450px; min-height: 250px;}
    .product__block-info{width: 45vw; row-gap: 20px;}
    .product__block-info--title{ font-size: 20px; width: 85%;} 
    .product__block-info--descr_block{width: 75%; height: 150px;}   
    .product__block-info--description{ font-size: 16px; width: 100%;} 
    .product__block-quantity--title{ font-size: 18px; margin-left: 30px;}
    .product__block-change--btn{ width: 40px; height: 40px; font-size: 16px;}  
    .product__block-change--indicator{font-size: 18px;} 
    .product__block-btns{ padding-top: 20px; column-gap: 15px;}    
    .product__block-btns--btn{column-gap: 5px; width: 17vw; height: 50px; font-size: 16px; }  
    .product__block-btn-image {width: 15px; height: 20px; margin-right: 5px;}
    .wish-image{ width: 25px; height: 20px; margin-right: 5px;}  
    .product__order-message{font-size: 14px;}
}
@media screen and (max-width: 860px){
    .product__block-navigator{top: 380px;}    
    .product__block-navigator--item{ width: 20vw; height: 60px; font-size: 16px; padding: 0px  10px; text-align: center;}   
    .product__block-btns--btn{column-gap: 5px; width: 17.5vw; height: 50px; font-size: 16px; }    
}
@include adaptive(tablet){
    .product__content-tracker{line-height: 200%;}
    .product__content-title{font-size: 45px; }
    .product__content-tracker, .shop__item-tracker-red{font-size: 16px;}
    .product__block-navigator{top: 360px;}    
    .product__block-navigator--item{ width: 18vw; height: 60px; font-size: 14px; padding: 0px  10px; text-align: center;} 
    
    .product__card{padding-block: 75px;}
    .product__block-image--block{min-height: 505px;}
    .product__block-image{min-width: 15vw; max-width: 35vw; max-height: 400px; min-height: 170px;}
    .product__block-trackers{width: 83px; height: 80px;}    
    .product__block-trackers--sale, .product__block-trackers--new{width: 83px; height: 40px; padding: 5px 10px; font-size: 16px;} 
    .product__block-info{width: 45vw; row-gap: 15px;}
    .product__block-info--stock{ font-size: 14px;}  
    .product__block-info--title{ font-size: 19px; width: 90%;} 
    .product__block-quantity--title{ font-size: 17px; margin-left: 20px;}    
    .product__block-info--price{column-gap: 15px;}    
    .product__block-price--old{font-size: 18px;} 
    .product__block-price--new{font-size: 24px;} 
    .product__block-info--descr_block{width: 83%; margin-top: 10px; height: 150px;}
    .product__block-order--quatity{row-gap: 10px; margin-top: 20px;} 
    .product__block-quantity-change{column-gap: 20px;}   
    .product__block-change--btn{ width: 36px; height: 36px; font-size: 16px;}    
    .product__block-change--indicator{font-size: 16px;}  
    .product__block-btns{ padding-top: 20px; column-gap: 10px;}    
    .product__block-btns--btn{column-gap: 5px; width: 18vw; height: 45px; font-size: 15px; }     
}
@media screen and (max-width: 650px){    
    .product__block-navigator{top: 350px;}    
    .product__block-navigator--item{ width: 24vw; height: 60px; font-size: 14px; padding: 0px  10px; text-align: center;}   
}
@include adaptive(phone){
    .product__intro-content{margin-left: 30px;}
    .product__content-title{font-size: 40px; }
    .product__content-tracker, .shop__item-tracker-red{font-size: 15px;}
    .product__block-navigator{top: 350px;}    
    .product__block-navigator--item{ width: 30vw; height: 50px; font-size: 13px; padding: 0px  5px; text-align: center;}  

    .product__card{padding-block: 90px; padding-bottom: 60px;}
    .product__card-block{width: 90vw; flex-direction: column; justify-content: flex-start; align-items: center; row-gap: 0;}  
    .product__block-image--block{min-height: 0; width: 90vw; height: 300px;}
    .product__block-image{min-width: 10vw; max-width: 50vw; max-height: 300px; min-height: 150px;}
    .product__block-trackers{width: 83px; height: 80px;}    
    .product__block-trackers--sale, .product__block-trackers--new{width: 83px; height: 40px; padding: 5px 15px; font-size: 15px;} 
    .product__block-info{width: 90vw; row-gap: 20px; padding: 30px; align-items: center;}
    .product__block-info--stock{ font-size: 14px;}  
    .product__block-info--title{ font-size: 20px; width: 100%; text-align: center;}  
    .product__block-info--descr_block{width: 90%; margin-left: 0; height: auto; overflow: hidden; border: none; text-align: center;}
    .product__block-info--price{column-gap: 10px;}    
    .product__block-price--old{font-size: 18px;} 
    .product__block-price--new{font-size: 24px;} 
    .product__block-order--quatity{row-gap: 10px; margin-top: 10px;} 
    .product__block-quantity--title{ font-size: 18px; margin-left: 22px;}    
    .product__block-change--btn{ width: 36px; height: 36px; font-size: 14px;}    
    .product__block-change--indicator{font-size: 18px;}  
    .product__block-btns{ padding-top: 30px; column-gap: 20px;}    
    .product__block-btns--btn{column-gap: 5px; width: 30vw; height: 50px; font-size: 16px; }  
    .product__block-btn-image {width: 15px; height: 20px; margin-right: 5px;}
    .wish-image{ width: 25px; height: 20px; margin-right: 5px;}      
}
@media screen and (max-width: 470px){
    .product__block-navigator--item{ width: 35vw; height: 50px; font-size: 13px; padding: 0px  5px; text-align: center;}     
    .product__block-info--title{width: 90%; font-size: 18px;}  
    .product__block-info--descr_block{width: 90%;}
    .product__block-quantity--title{ font-size: 17px; margin-left: 22px;} 
    .product__block-change--indicator{font-size: 17px;}
    .product__block-btns--btn{column-gap: 5px; width: 33vw; height: 45px; font-size: 15px; }  
}
