@import '../../../styles/scss_common/mixins';

.contact-message{
	position: relative;
	width: 100vw;
	height: 800px;
	display: flex;
    flex-direction: row;  
    justify-content: flex-end;
    align-items: center;
	column-gap: 80px;	
	background-color: #FCEDEA;
}

.contact-message__image-block{
	position: absolute;	
	left: -15%;
	bottom: 0;
	height: 800px;
	align-self: end;	
	opacity: 90%;
	z-index: 25;
}

.contact-message__block{
	width: 350px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 30px;
}

.contact-message__logo, .contact-message__title, .contact-message__text{
	margin: 0;
	z-index: 25;
}

.contact-message__logo{font-size: 50px;}

.contact-message__title{margin-top: -20px; font-size: 100px;}

.contact-message__text{font-size: 20px;}

.contact-message__form{	
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 10px;
	z-index: 25;
}

.contact-message__input, .contact-message__form-text{
	width: 350px;
	height: 50px;
	border: 1px solid #FCEDEA;
	outline: none;
	background-color: #FFFFFF;
	padding-inline: 30px;
	padding-block: 18px;
	font-size: 16px;
	line-height: 150%;
	color: #666666;
	box-sizing: border-box;
}

.contact-message__form-text{	
	height: 90px;
	resize: none;
	overflow-y: auto;
	outline: none;
	border: none;
}

#message-text{
	outline: none;
	border: none;
}

.contact-message__btn{
	width: 168px;
	height: 60px;
	background-color: #222222;
	padding: 23px 50px;
	font-size: 14px;
	line-height: 100%;
	text-transform: uppercase;
	color: #FFFFFF;
	margin-top: -10px;	
	outline: none;
	border: none;
}

.contact-message__form-message{
	font-size: 16px;
	line-height: 100%;
	color: #D05278;
	margin-left: 15px;
}

.contact-message__decorator{
	position: absolute;
	bottom: -4%;
	right: -8%;
	height: 400px;
	opacity: 70%;
	transform: rotate(5deg);
}

@media screen and (max-width: 4800px){
	.contact-message{height: 1000px; column-gap: 80px;}
	.contact-message__image-block{left: -5%; height: 1000px; opacity: 80%;}	
	.contact-message__block{ width: 60vw; row-gap: 35px;}
	.contact-message__logo{font-size: 100px;}
	.contact-message__title{margin-top: -30px; font-size: 90px;}	
	.contact-message__text{font-size: 32px; width: 40%;}
	.contact-message__form{row-gap: 20px;}	
	.contact-message__input, .contact-message__form-text{width: 850px; height: 100px; border: 1px solid #FCEDEA; padding-inline: 30px; padding-block: 18px; font-size: 32px;}	
	.contact-message__form-text{height: 140px;}	
	.contact-message__btn{width: 50%; height: 90px; padding: 23px 50px; font-size: 34px; margin-top: -20px;}	
	.contact-message__form-message{font-size: 28px; margin-left: 15px;}	
	.contact-message__decorator{bottom: -10%; right: -3%; height: 1000px; opacity: 65%; transform: rotate(15deg);}
}
