@import '../../styles/scss_common/mixins';

.touch{
    width: 100vw;    
    background-color: #FFFFFF;    
    z-index: 14;
    display: flex;    
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.touch__content{
    width: 80vw;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-color: #FCEDEA;
}

.touch__content-bg-image{
    position: absolute;
    top: -40px;
    left: -14px;
    width: 524px;
    height: 450px;
    z-index: 16;
}
.touch__content-img{
    position: absolute;
    top: 8px;
    left: 60px;
    width: auto;
    height: 302px;
    z-index: 17;
    object-fit: cover;
}

.touch__content-block{
    width: 60vw; 
    height: 100%;   
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 30px;

}
.touch__block-title{    
    font-size: 40px;
    line-height: 140%;;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
    z-index: 18;
}

.touch__block-text{
    margin: 0;
    margin-top: -28px;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    z-index: 18;
    width: 90%;
}

.touch__block-subscription{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 10px;   
    width: 565px;
    z-index: 18;
}
.touch__subscription-input{
    width: 380px;
    height: 60px;
    border: none;
    outline: none;
    color: #999999;
    padding-block: 18px;
    padding-left: 30px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 170%;  
    z-index: 18;  
}
.touch__subscription-btn, .touch__subscription-btn--link{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 175px;
    height: 60px;
    background-color: #222222;
    padding: 23px 50px;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;
    z-index: 18;
    box-sizing: border-box;
    text-decoration: none;
    cursor: pointer;
}

.touch__block-message{
    font-size: 16px;
    line-height: 100%;
    color: #D05278;
    z-index: 25;
    margin-top: -5px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}

