@import '../../../styles/scss_common/mixins';

.about{
    width: 100vw;
    position: relative;
    background-color: #FFFFFF;   
    z-index: 1;    
}
.about__intro{
    position: relative;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;   
}

.about__intro-advantages{
    width: 1140px;
    height: 120px;    
    margin-top: -60px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 30px; 
    z-index: 45;   
}

.about__advantages-item{
    width: 360px;
    height: 120px;
    background-color: #FAF9FF;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 42px;  
    z-index: 13;  
}
.about__item-image--block{
    position: relative;
    width: 50px;
    height: 50px;
}
.about__block-img--background{
    z-index: 13;
    width: 80px;
    height: 67px;
    position: absolute;
    left: -15px;
    top: -8px;
}

.about__block-img{
    width: 50px;
    height: 50px;
    z-index: 14;
}

.about__item-info{    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 1px;
}
.about__info-title{
    font-size: 20px;
    line-height: 120%;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
}

.about__info-text{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    margin: 0;
    text-decoration: none;
}

@media screen  and (max-width: 4800px){
    .about__intro-advantages{width: 2280px; height: 180px; column-gap: 50px; margin-top: -90px; }    
    .about__advantages-item{width: 720px; height: 180px; column-gap: 84px;}
    .about__item-image--block{width: 100px; height: 100px;}
    .about__block-img--background{width: 160px; height: 134px; left: -30px; top: -16px;}    
    .about__block-img{ width: 100px; height: 100px;}    
    .about__item-info{row-gap: 2px;}
    .about__info-title{font-size: 40px;}    
    .about__info-text{ font-size: 32px;}
}
@media screen  and (max-width: 2800px){
    .about__intro-advantages{width: 1860px; column-gap: 40px;}    
    .about__advantages-item{width: 600px; column-gap: 64px;}
    .about__item-image--block{width: 80px; height: 80px;}
    .about__block-img--background{width: 130px; height: 105px; left: -24px; top: -12px;}    
    .about__block-img{ width: 80px; height: 80px;}    
    .about__item-info{row-gap: 2px;}
    .about__info-title{font-size: 34px;}    
    .about__info-text{ font-size: 30px;}
}
@media screen  and (max-width: 2350px){
    .about__intro-advantages{width: 1580px; column-gap: 40px; margin-top: -90px; }    
    .about__advantages-item{width: 500px;}
    .about__item-image--block{width: 70px; height: 70px;}
    .about__block-img--background{width: 110px; height: 95px; left: -20px; top: -9px;}    
    .about__block-img{ width: 70px; height: 70px;}    
}
@media screen  and (max-width: 1800px){
    .about__intro-advantages{width: 80vw; height: 120px; column-gap: 30px; margin-top: -60px; }    
    .about__advantages-item{width: 26vw; height: 120px; column-gap: 42px;}
    .about__item-image--block{width: 50px; height: 50px;}
    .about__block-img--background{width: 80px; height: 67px; left: -15px; top: -8px;}    
    .about__block-img{ width: 50px; height: 50px;}    
    .about__item-info{row-gap: 1px; flex-basis: 55%;}
    .about__info-title{font-size: 24px;}    
    .about__info-text{ font-size: 20px;}
}
@media screen  and (max-width: 1500px){    
    .about__info-title{font-size: 20px;}    
    .about__info-text{ font-size: 16px;}
}
@media screen  and (max-width: 1200px){     
    .about__advantages-item{column-gap: 32px;} 
    .about__item-image--block{width: 40px; height: 40px;}   
    .about__block-img--background{width: 60px; height: 47px; left: -9px; top: -5px;}    
    .about__block-img{ width: 40px; height: 40px;}    
    .about__info-title{font-size: 18px;}    
    .about__info-text{ font-size: 14px;}
}
@include adaptive(laptop){
    .about__intro-advantages{width: 100vw; height: 120px; column-gap: 15px; margin-top: -60px; justify-content: center;} 
    .about__advantages-item{width: 30vw; height: 120px; column-gap: 44px;}
}
@media screen  and (max-width: 850px){     
    .about__advantages-item{column-gap: 22px;}   
}
@include adaptive(tablet){
    .about__intro-advantages{flex-wrap: wrap; margin-top: -60px; height: auto;} 
    .about__advantages-item{flex-basis: 100%; width: 100vw; column-gap: 94px; height: 145px; border-block: 2px solid #EEEEEE;} 
    .about__item-image--block{width: 80px; height: 80px;}   
    .about__block-img--background{width: 110px; height: 77px; left: -9px; top: -5px;}    
    .about__block-img{ width: 80px; height: 80px;}    
    .about__info-title{font-size: 28px;}    
    .about__info-text{ font-size: 24px;}
    .helpline-contacts{order: 1;}
}
@include adaptive(phone){    
    .about__advantages-item{ column-gap: 74px; height: 145px; border-block: 2px solid #EEEEEE; padding-inline: 15px;} 
    .about__item-image--block{width: 60px; height: 60px;}   
    .about__block-img--background{width: 90px; height: 77px; left: -9px; top: -9px;}    
    .about__block-img{ width: 60px; height: 60px;}    
    .about__info-title{font-size: 24px;}    
    .about__info-text{ font-size: 20px;}   
}
@media screen  and (max-width: 400px){
    .about__advantages-item{ column-gap: 54px; height: 125px; border-block: 2px solid #EEEEEE;} 
    .about__item-image--block{width: 50px; height: 50px;}   
    .about__block-img--background{width: 80px; height: 67px; left: -9px; top: -9px;}    
    .about__block-img{ width: 50px; height: 50px;}    
    .about__info-title{font-size: 22px;}    
    .about__info-text{ font-size: 18px;}
}


