@import '../../../styles/scss_common/mixins';

.discount{
    width: 100vw;
    height: 800px;    
    position: relative;  
    overflow: hidden; 
    background-color: #FCEDEA;  
}

.discount__content{
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 82px;
}
.discount__content-img{    
    position: absolute;    
    bottom: -50px;
    left: -12%;
    height: 850px;
    opacity: 90%;
}

.discount__content-info{
    width: 23vw;
    position: absolute;
    top: 6%;
    right: 20%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
    z-index: 15;
}

.discount__content-logo{
    font-size: 60px;
    line-height: 100%;
    color: #D05278;
    margin: 0;
    z-index: 15;
}
.discount__content-title{
    width: 434px;
    font-size: 80px;
    line-height: 100%;
    color: #222222;
    margin: 0;
    margin-top: -14px;   
    margin-left: -5px; 
    z-index: 15;
}

#red{
    z-index: 15;
    color: #D05278; 
    background-image: url(../../../assets/icons/discount_devider.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    display: inline-block;
}

.discount__content-text{
    width: 434px;
    font-size: 20px;
    line-height: 150%;
    color: #666666;
    margin: 0;
    z-index: 15;
}

.discount__content-btn{
    width: 179px;
    height: 60px;  
    padding: 23px;  
    background-color: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
    z-index: 15;
}
.discount__btn-link{
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;
    text-decoration: none;
    padding: 30px;
}

.discount__decorator{
    position: absolute;
    bottom: -12%;
    right: -10%;
    height: 500px;
    opacity: 70%;
    transform: rotate(30deg);
}

@media screen and (max-width: 4800px) {    
    .discount__content-img{ left: 5%;}    
    .discount__content-info{width: 30vw; top: 15%; left: 40%; row-gap: 50px;}    
    .discount__content-logo{font-size: 100px; align-self: center;}
    .discount__content-title{ width: 50vw; font-size: 120px;}
    .discount__content-text{ width: 30vw; font-size: 34px; text-align: center;}  
    .discount__content-btn{width: 13vw; height: 90px; align-self: center; margin-top: 30px; padding: 10px 15px;} 
    .discount__btn-link{font-size: 32px; padding: 10px 15px;} 
    .discount__decorator{right: -1%; bottom: -17%; height: 700px;} 
    #red{margin-inline: 25px;}
}
@media screen and (max-width: 3000px) {  
    .discount__content-img{ left: 2%;}      
    .discount__content-info{width: 30vw; top: 15%; left: 40%;}    
    .discount__content-logo{font-size: 90px;}
    .discount__content-title{ width: 50vw; font-size: 100px;}
    .discount__content-text{ width: 35vw; font-size: 30px;}   
    .discount__decorator{right: -5%; bottom: -17%; height: 700px;}  
}
@media screen and (max-width: 2500px){
    .discount__content-img{ left: -5%;}      
    .discount__content-info{width: 35vw; top: 15%; left: 40%; align-items: center;}    
    .discount__content-logo{font-size: 80px; width: 100%; text-align: center;}
    .discount__content-title{ width: 100%; font-size: 80px;  text-align: center;}
    .discount__content-text{  width: 100%; font-size: 28px;}  
    .discount__decorator{right: -5%; bottom: -17%; height: 600px;}  
    .discount__content-btn{width: 16vw; height: 80px; align-self: center; margin-top: 30px; padding: 10px 15px;} 
    .discount__btn-link{font-size: 28px; padding: 10px 15px;} 
}
@media screen and (max-width: 2000px){
    .discount{height: 700px;}
