    .discount__content-info{width: 40vw; top: 15%; left: 38%;}    
    .discount__content-logo{font-size: 70px;}
    .discount__content-title{font-size: 70px;}
    .discount__content-text{font-size: 26px;}  
    .discount__content-btn{width: 20vw;}
    .discount__btn-link{font-size: 24px; padding: 10px 15px;} 
