@media screen and (max-width: 1200px){
    .discount__content-img{ left: -25%;}
    .discount__content-info{top: 13%; left: 40%; width: 55vw; } 
    .discount__content-logo{ font-size: 60px;}
    .discount__content-title{width: 38vw; font-size: 60px;}   
    .discount__content-text{ width: 42vw; font-size: 24px;} 
