    .discount__content-text{ width: 50vw; font-size: 24px;} 
    .discount__content-btn{width: 25vw; height: 70px;} 
    .discount__btn-link{font-size: 22px;}   
}
@media screen and (max-width: 800px){
    .discount__content-img{ left: -22%;}
    .discount__content-info{top: 13%; left: 35%; width: 65vw; } 
    .discount__content-logo{ font-size: 60px;}
