@import '../../../styles/scss_common/mixins';

.contact{
    width: 100vw;    
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #FFFFFF;  
    z-index: 1;   
}

.contact__intro{
    position: relative;     
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; 
}

.contact__intro-advantages{
    width: 1140px;
    height: 120px;    
    align-self: center;    
    margin-top: -60px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 30px; 
    z-index: 45;   
}

.contact__advantages-item{
    width: 360px;
    height: 120px;
    background-color: #FAF9FF;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 42px;    
}
.contact__item-image--block{
    position: relative;
    width: 50px;
    height: 50px;
}
.contact__block-img--background{
    z-index: 13;
    width: 80px;
    height: 67px;
    position: absolute;
    left: -15px;
    top: -8px;
}

.contact__block-img{
    width: 50px;
    height: 50px;
    z-index: 14;
}

.contact__item-info{   
    flex-basis: 55%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 1px;
}
.contact__info-title{
    font-size: 20px;
    line-height: 120%;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
}

.contact__info-text{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    margin: 0;
    text-decoration: none;
}


@media screen  and (max-width: 4800px){
    .contact__intro-advantages{width: 70vw; height: 180px; column-gap: 50px; margin-top: -90px; }    
    .contact__advantages-item{width: 23vw; height: 180px; column-gap: 84px; padding: 20px;}
    .contact__item-image--block{width: 100px; height: 100px;}
    .contact__block-img--background{width: 160px; height: 134px; left: -30px; top: -16px;}    
    .contact__block-img{ width: 100px; height: 100px;}    
    .contact__item-info{row-gap: 2px;}
    .contact__info-title{font-size: 40px;}    
    .contact__info-text{ font-size: 32px;}
}
@media screen  and (max-width: 3000px){       
    .contact__advantages-item{column-gap: 64px;}  
    .contact__info-text{ font-size: 30px;}  
}
@media screen  and (max-width: 2500px){
    .contact__intro-advantages{width: 70vw; column-gap: 40px;}    
    .contact__advantages-item{width: 23vw; padding: 10px;  box-sizing: border-box;}
    .contact__item-image--block{width: 60px; height: 60px;}
    .contact__block-img--background{width: 110px; height: 95px; left: -15px; top: -12px;}    
    .contact__block-img{ width: 60px; height: 60px;}  
    .contact__item-info{flex-basis: 60%;}
    .contact__info-title{font-size: 28px;}    
    .contact__info-text{ font-size: 24px;}   
}
@media screen  and (max-width: 2000px){
    .contact__intro-advantages{height: 120px; column-gap: 30px; margin-top: -60px; }    
    .contact__advantages-item{width: 23vw; height: 120px; column-gap: 42px;}
    .contact__item-image--block{width: 50px; height: 50px;}
    .contact__block-img--background{width: 80px; height: 67px; left: -15px; top: -8px;}    
    .contact__block-img{ width: 50px; height: 50px;}    
    .contact__item-info{row-gap: 1px; flex-basis: 55%;}
    .contact__info-title{font-size: 22px;}    
    .contact__info-text{ font-size: 18px;}
}
@media screen  and (max-width: 1500px){
    .contact__intro-advantages{ width: 80vw;}    
    .contact__advantages-item{width: 26vw; column-gap: 30px;}   
    .contact__info-title{font-size: 20px;}    
    .contact__info-text{ font-size: 16px;}  
}
@media screen  and (max-width: 1200px){  
    .contact__intro-advantages{ width: 85vw;}    
    .contact__advantages-item{width: 28vw;}   
    .contact__item-image--block{width: 45px; height: 45px;}   
    .contact__block-img--background{width: 70px; height: 57px; left: -9px; top: -8px;}    
    .contact__block-img{ width: 45px; height: 45px;}    
    .contact__info-title{font-size: 18px;}    
    .contact__info-text{ font-size: 14px;}
}
@include adaptive(laptop){
    .contact__intro-advantages{width: 98vw; height: 120px; column-gap: 10px; margin-top: -60px; justify-content: center;} 
    .contact__advantages-item{width: 32vw; height: 120px; column-gap: 30px;}
    .contact__item-image--block{width: 40px; height: 40px;}   
    .contact__block-img--background{width: 60px; height: 47px; left: -6px; top: -7px;}    
    .contact__block-img{ width: 40px; height: 40px;}    
    .contact__item-info{flex-basis: 60%;}    
}
@media screen  and (max-width: 850px){     
    .contact__advantages-item{column-gap: 22px;}   
    .contact__info-title{font-size: 16px;}    
    .contact__info-text{ font-size: 13px;}
}
@include adaptive(tablet){
    .contact__intro-advantages{flex-wrap: wrap; margin-top: -60px; height: auto; width: 100vw;} 
    .contact__advantages-item{flex-basis: 100%; width: 100vw; column-gap: 94px; height: 145px; border-block: 2px solid #EEEEEE;} 
    .phone-email-contacts{order: 1;}
    .contact__item-image--block{width: 50px; height: 50px;}   
    .contact__block-img--background{width: 80px; height: 67px; left: -15px; top: -8px;}    
    .contact__block-img{ width: 50px; height: 50px;}    
    .contact__info-title{font-size: 24px;}    
    .contact__info-text{ font-size: 20px;}
}
@include adaptive(phone){    
    .contact__advantages-item{ column-gap: 64px; height: 145px; border-block: 2px solid #EEEEEE; padding-inline: 15px;}        
    .contact__info-title{font-size: 22px;}    
    .contact__info-text{ font-size: 18px;}
}
@media screen  and (max-width: 400px){
    .contact__advantages-item{ column-gap: 50px; height: 125px; border-block: 2px solid #EEEEEE;} 
    .contact__item-image--block{width: 40px; height: 40px;}   
    .contact__block-img--background{width: 60px; height: 47px; left: -7px; top: -7px;}    
    .contact__block-img{ width: 40px; height: 40px;}    
    .contact__info-title{font-size: 18px;}    
    .contact__info-text{ font-size: 16px;}
}