@import '../../../styles/scss_common/mixins';

.profile-wishes{
	width: 100vw;    
    position: relative;
    background-color: #FCEDEA;
    z-index: 1;
}

.profile-wishes__body{
	position: relative;
    width: 100vw;
    padding-bottom: 60px;   
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 50px;
    background-color: #FFFFFF;
}

.profile-wishes--decor{
	position: absolute;
    top: 0%;
    left: -18%;
    height: 500px;    
    opacity: 65%;
    z-index: 25;
}

.profile-wishes--decor2{
	position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-10deg);
    opacity: 65%;
    z-index: 25;
}

.profile-wishes__block{
	width: 75vw;
    min-height: 600px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;     
}

.profile-wishes-components{
	width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 65px;
}

.profile-wishes-components-gallery{
    width: 100vw;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 5px;
}

@media screen and (max-width: 4800px){
    .profile-wishes__body{padding-block: 180px;}    
    .profile-wishes--decor{top: 30%; left: -11%; height: 900px; opacity: 65%;}    
    .profile-wishes--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}    
    .profile-wishes__block{width: 75vw; min-height: 600px; row-gap: 180px;}   
    .profile-wishes-components{width: 100vw; row-gap: 65px;}   
    .profile-wishes-components-gallery{row-gap: 5px;}    
}
