@import '../../../styles/scss_common/mixins';

.order{
    width: 100vw;   
    position: relative;   
    background-color: #FCEDEA;
    z-index: 1;
}

.order-bg1{
    position: absolute;
    top: 30%;
    left: -22%;
    height: 600px;    
    opacity: 65%;
    z-index: 25;
}

.order-bg2{
    position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-30deg);
    opacity: 65%;
    z-index: 25;
}

.order__intro{
    position: relative;
    width: 100vw;   
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.order__intro-steps{
    width: 70vw;
    height: 120px;
    position: absolute;
    bottom: -60px;
    left: calc(100% -(70vw / 2));
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 30px;   
    z-index: 25;
}

.order__intro-steps--item{
    width: 23vw;
    height: 120px;
    background-color: #FAF9FF; 
    border: 1px solid #EEEEEE;   
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 42px;  
    z-index: 25; 
}

.order__steps-item--image-block{
    position: relative;
    width: 50px;
    height: 50px;
}

.order__steps-image--block-img{
    width: 50px;
    height: 50px;
    z-index: 26;
}

.order__steps-image--block-vector{
    z-index: 25;
    width: 80px;
    height: 67px;
    position: absolute;
    left: -15px;
    top: -8px;
}


@media screen and (max-width: 4800px){    
    .order-bg1{top: 30%; left: -13%; height: 1000px; opacity: 65%;}
    .order-bg2{bottom: 0%; right: 0%;  height: 700px; transform: rotate(-0deg); opacity: 65%;}
    .order__intro-steps{ width: 70vw; height: 180px; bottom: -90px; left: calc(100% -(70vw / 2)); column-gap: 50px;}
    .order__intro-steps--item{width: 23vw; height: 180px; column-gap: 84px;}
    .order__steps-item--image-block{width: 90px; height: 90px;}
    .order__steps-image--block-img{ width: 90px; height: 90px;}
    .order__steps-image--block-vector{width: 150px; height: 124px; left: -28px; top: -15px;}
}
