@import '../../../styles/scss_common/mixins';

.wish__content-block{
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
    position: relative;       
}
.wish__block-head{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 50px;
    padding-bottom: 5px;
    border-bottom: 2px solid #222222;
    z-index: 15;
    
}

.wish__head-item{
    margin: 0;
    font-size: 20px;
    line-height: 120%;
    text-transform: capitalize;
    color: #222222;
}

.wish-item-product, .wish__item-product{flex-basis: 40%;}
.wish-item-price, .wish__item-price{flex-basis: 12%;}
.wish-item-status, .wish__item-status{ flex-basis: 20%;}
.wish-item-link, .wish__item-link{flex-basis: 15%;}

.wish__block-body{
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;
    color: #222222;
    font-size: 16px;
    line-height: 120%;    
}

.wish__body-item{
    width: 80vw;
    height: 170px;
    background-color: #FFFFFF;
    border: 2px solid #EEEEEE;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: center;
    column-gap: 70px;
    box-sizing: border-box;
}
.wish__item-product{        
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 30px;
}

.wish__product-image{
    min-height: 150px; 
    max-height: 300px; 
    min-width: 4vw; 
    max-width: 15vw; 
    flex-basis: 17%;
}
.wish__product-info{    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 5px;
}

.wish__product-info--title{
    font-size: 24px;
    line-height: 100%;
    color: #000000;
    text-transform: capitalize;
    margin: 0;
    margin-bottom: 10px;
}

.wish__product-info--sku{
    margin: 0;
    font-size: 12px;
    line-height: 170%;    
    color: #666666;
}

.wish__item-price{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    row-gap: 10px;
}

.wish__price-old{
    font-size: 14px;
    line-height: 100%;
    text-decoration: line-through;
    text-transform: capitalize;
    color: #999999;
    margin: 0;
}
.wish__price-new{
    font-size: 18px;
    line-height: 100%;    
    text-transform: capitalize;
    color: #666666;
    margin: 0;
}

.wish__item-status{    
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}

.wish__status-item{
    font-size: 12px;
    line-height: 170%;
    text-transform: uppercase;
}

.wish__item-link{
    height: 100%;
    display: flex;
    flex-direction: column;    
    justify-content: center;
    align-items: flex-start;
}

.wish__link-item{
    font-size: 14px;
    line-height: 170%;
    text-transform: uppercase;
    color: #D05278;
    text-decoration: none;
}

.wish__block-body--message{
    margin: 0;    
    color: #D05278;
    font-size: 20px;
    line-height: 130%; 
    align-self: center;
    text-align: center;
    letter-spacing: 0.85px;
}

@media screen and (max-width: 4800px){
    .wish__content-block{width: 75vw; row-gap: 60px; padding-bottom: 80px;}
    .wish__block-head{ column-gap: 80px;  padding-bottom: 10px;  border-bottom: 4px solid #222222; justify-content: flex-start; width: 100%; padding-inline: 40px; box-sizing: border-box;}   
    .wish__head-item{font-size: 40px; line-height: 120%;}
    .wish-item-product, .wish__item-product{flex-basis: 45%; width: 45%;}     
    .wish-item-price, .wish__item-price{flex-basis: 15%;}   
    .wish-item-status, .wish__item-status{flex-basis: 25%;}    
    .wish-item-total, .wish__item-link{flex-basis: 15%;}      
    .wish__block-body{ width: 75vw; row-gap: 40px; font-size: 32px; line-height: 120%; justify-content: flex-start;}    
    .wish__body-item{width: 75vw; height: 340px; border: 4px solid #EEEEEE; padding: 40px; column-gap: 70px; }
    .wish__item-product{ column-gap: 80px;}    
    .wish__product-image{ min-height: 150px; max-height: 250px; min-width: 4vw; max-width: 15vw; flex-basis: 17%;}
    .wish__product-info{ row-gap: 10px; flex-basis: 80%;}    
    .wish__product-info--title{font-size: 32px; margin-bottom: 10px; width: 85%;}    
    .wish__product-info--sku{font-size: 28px; line-height: 170%; }    
    .wish__item-price{row-gap: 20px;}    
    .wish__price-old{font-size: 30px;}
    .wish__price-new{font-size: 36px;}    
    .wish__item-status{ column-gap: 40px;}    
    .wish__status-item{ font-size: 32px; line-height: 170%;}   
    .wish__item-link{height: 100%;}    
    .wish__link-item{font-size: 32px; line-height: 170%;}    
    .wish__block-body--message{ font-size: 44px; line-height: 130%; letter-spacing: 0.85px;}
}
@media screen and (max-width: 3500px){      
    .wish__product-image{min-height: 150px; max-height: 250px; min-width: 4vw; max-width: 15vw; flex-basis: 20%;}    
}
@media screen and (max-width: 3000px){
    .wish__block-head, .wish__body-item{ column-gap: 60px;}  
    .wish__block-head{border-bottom: 3px solid #222222;}
    .wish__head-item{font-size: 36px;}
    .wish__product-image{min-height: 150px; max-height: 250px; min-width: 3vw; max-width: 25vw; flex-basis: 25%;}  
    .wish__product-info--title{font-size: 30px; margin-bottom: 10px; width: 85%;}    
    .wish__product-info--sku{font-size: 26px; line-height: 170%; }       
    .wish__price-old{font-size: 26px;}
    .wish__price-new{font-size: 32px;}    
    .wish__status-item{ font-size: 30px;}  
    .wish__link-item{font-size: 30px;}    
    .wish__block-body--message{ font-size: 40px;}
}
@media screen and (max-width: 2500px){
    .wish__content-block{row-gap: 40px;}
    .wish__block-head, .wish__body-item{column-gap: 50px;}
    .wish__block-head{border-bottom: 3px solid #222222;}
    .wish__head-item{font-size: 32px; line-height: 120%;} 
    .wish__body-item{height: 280px;}    
    .wish__product-image{flex-basis: 25%; max-height: 260px; min-height: 130px; }
    .wish__product-info{ row-gap: 5px; flex-basis: 75%;}    
    .wish__product-info--title{font-size: 32px; margin-bottom: 10px; width: 85%;}    
    .wish__product-info--sku{font-size: 28px; line-height: 170%; }    
    .wish__item-price{row-gap: 20px;}    
    .wish__price-old{font-size: 30px;}
    .wish__price-new{font-size: 36px;}    
    .wish__item-status{ column-gap: 30px;}    
    .wish__status-item{ font-size: 28px;}     
    .wish__link-item{font-size: 28px;}    
    .wish__block-body--message{ font-size: 38px;}
}
@media screen and (max-width: 2000px){
    .wish__content-block{row-gap: 40px; width: 80vw;}
    .wish__block-head, .wish__body-item{column-gap: 60px;}    
    .wish__head-item{font-size: 26px; line-height: 120%;} 
    .wish__block-body, .wish__body-item{width: 80vw;}
    .wish__body-item{height: 240px;}    
    .wish__product-image{flex-basis: 30%; max-height: 200px; min-height: 120px; min-width: 3vw; max-width: 30vw;}
    .wish__product-info{ row-gap: 5px; flex-basis: 70%;}    
    .wish__product-info--title{font-size: 26px; margin-bottom: 10px; width: 85%;}    
    .wish__product-info--sku{font-size: 22px; line-height: 170%; }     
    .wish__price-old{font-size: 24px;}
    .wish__price-new{font-size: 30px;}        
    .wish__status-item{ font-size: 22px;}    
    .wish__link-item{font-size: 22px;}    
    .wish__block-body--message{ font-size: 30px;}
}
@media screen and (max-width: 1700px){
    .wish__block-head{border-bottom: 2px solid #222222;}  
    .wish__product-image{flex-basis: 30%; max-height: 200px; min-height: 100px; min-width: 3vw; max-width: 30vw;}
    .wish__head-item, .wish__product-info--title{font-size: 24px;} 
    .wish__product-info--sku, .wish__status-item, .wish__link-item{font-size: 20px;}
    .wish__price-old{font-size: 22px;}
    .wish__price-new{font-size: 28px;}    
}
@media screen and (max-width: 1560px){ 
    .wish__content-block{row-gap: 30px; width: 80vw;} 
    .wish__block-head, .wish__body-item{column-gap: 30px;}     
    .wish__block-body{row-gap: 20px;}
    .wish__head-item{font-size: 22px;} 
    .wish__body-item{height: 170px; padding: 20px; width: 80vw;}   
    .wish-item-product, .wish__item-product{flex-basis: 50%; width: 50%; height: 100%;}     
    .wish-item-price, .wish__item-price{flex-basis: 10%; height: 100%;}   
    .wish-item-status, .wish__item-status{flex-basis: 15%; height: 100%;}    
    .wish-item-total, .wish__item-link{flex-basis: 20%; height: 100%;} 
    .wish__item-product{column-gap: 40px;}  
    .wish__product-image{flex-basis: 19%; max-height: 120px; min-height: 100px; min-width: 2vw; max-width: 30vw;}
    .wish__product-info{ row-gap: 5px; flex-basis: 70%;}    
    .wish__product-info--title{font-size: 22px; margin-bottom: 10px; width: 85%;}    
    .wish__product-info--sku{font-size: 18px;}       
    .wish__price-old{font-size: 22px;}
    .wish__price-new{font-size: 26px;}    
    .wish__status-item{ font-size: 18px;}     
    .wish__link-item{font-size: 18px;}    
    .wish__block-body--message{ font-size: 24px;}
}
@media screen and (max-width: 1360px){.wish__product-image{flex-basis: 20%; max-height: 120px; min-height: 100px; min-width: 2vw; max-width: 30vw;}}
@media screen and (max-width: 1260px){  
    .wish__content-block{padding-bottom: 30px;}     
    .wish__head-item{font-size: 20px;}  
    .wish__item-product{column-gap: 30px;} 
    .wish__body-item{height: 150px;}  
    .wish__product-image{flex-basis: 23%; max-height: 100px; min-height: 80px; min-width: 2vw; max-width: 30vw;}  
    .wish__product-info{row-gap: 0;} 
    .wish__product-info--title{font-size: 17px;}    
    .wish__product-info--sku{font-size: 15px;} 
    .wish__item-price{row-gap: 12px;}        
    .wish__price-old{font-size: 16px;}
    .wish__price-new{font-size: 22px;}     
    .wish__status-item{ font-size: 15px;}      
    .wish__link-item{font-size: 15px;}    
    .wish__block-body--message{ font-size: 24px;}
}
@include adaptive(laptop){ 
    .wish__content-block{width: 85vw;}
    .wish__block-body, .wish__body-item{width: 85vw;}
    .wish__head-item{font-size: 17px; line-height: 120%;}
    .wish__block-head{border-bottom: 1px solid #222222;}   
    .wish__body-item{height: 140px; padding: 20px; width: 85vw; border: 3px solid #EEEEEE;}   
    .wish-item-product, .wish__item-product{flex-basis: 45%; width: 45%; height: 100%;}     
    .wish-item-price, .wish__item-price{flex-basis: 12%; height: 100%;}   
    .wish-item-status, .wish__item-status{flex-basis: 18%; height: 100%;}    
    .wish-item-total, .wish__item-link{flex-basis: 20%; height: 100%;}    
    .wish__product-image{flex-basis: 32%; max-height: 100px; min-height: 60px; max-width: 30vw; min-width: 2vw;}     
    .wish__product-info--title{font-size: 14px;}    
    .wish__product-info--sku{font-size: 12px;}    
    .wish__item-price{row-gap: 9px;}   
    .wish__price-old{font-size: 13px;}
    .wish__price-new{font-size: 16px;}      
    .wish__status-item{ font-size: 13px;}   
    .wish__link-item{font-size: 13px;}    
    .wish__block-body--message{ font-size: 20px;}
}
@include adaptive(tablet){   
    .wish__content-block{padding-bottom: 0; row-gap: 30px;}   
    .wish__block-head, .wish__body-item{column-gap: 30px; } 
    .wish__block-head{border-bottom: 1px solid #222222; padding-bottom: 6px; padding-inline: 10px;}  
    .wish__head-item{font-size: 15px; line-height: 120%;} 
    .wish__body-item{height: auto; padding: 20px; width: 85vw; padding-inline: 10px;}   
    .wish-item-product, .wish__item-product{flex-basis: 58%; width: 58%; height: 100%;}     
    .wish-item-price, .wish__item-price{display: none;}   
    .wish-item-status, .wish__item-status{flex-basis: 13%; height: 100%;}    
    .wish-item-total, .wish__item-link{flex-basis: 25%; height: 100%;}   
    .wish-item-product{column-gap: 40px;}
    .wish__product-image{flex-basis: 32%; max-height: 90px; min-height: 60px;}  
    .wish__product-info{row-gap: 0; }   
    .wish__product-info--title{font-size: 13.5px; width: 95%; line-height: 140%; margin-bottom: 3px;}    
    .wish__product-info--sku{font-size: 12px;}        
    .wish__status-item{ font-size: 11.5px;}   
    .wish__link-item{font-size: 11.5px;}    
    .wish__block-body--message{ font-size: 18px;}
}
@include adaptive(phone){   
    .wish__content-block{padding-bottom: 0; row-gap: 20px;}   
    .wish__block-head, .wish__body-item{column-gap: 40px; } 
    .wish__block-body{row-gap: 15px;}
    .wish__block-head{border-bottom: 1px solid #222222; padding-bottom: 6px; padding-inline: 10px;}  
    .wish__head-item{font-size: 15px; line-height: 120%;} 
    .wish__body-item{height: auto; padding: 15px; width: 85vw; padding-inline: 10px;}   
    .wish-item-product, .wish__item-product{flex-basis: 70%; width: 70%; height: 100%;}     
    .wish-item-status, .wish__item-status{display: none;}  
    .wish-item-total, .wish__item-link{flex-basis: 27%; height: 100%;} 
    .wish__item-product{column-gap: 20px;}     
    .wish__item-price{row-gap: 12px;}
    .wish__product-image{flex-basis: 25%; max-height: 70px; min-height: 50px;}  
    .wish__product-info{row-gap: 0;}   
    .wish__product-info--title{font-size: 13px; width: 95%; line-height: 160%;}    
    .wish__product-info--sku{font-size: 11px;}    
    .wish__link-item{font-size: 10px; align-self: flex-end; padding-right: 15px;}    
    .wish__block-body--message{ font-size: 16px;}
}
@media screen and (max-width: 450px){
    .wish__content-block, .wish__block-body, .wish__body-item{width: 88vw;}    
    .wish__block-head, .wish__body-item{column-gap: 30px; } 
    .wish__head-item{font-size: 14px;}    
    .wish__product-info--title{font-size: 12px; width: 100%; margin: 0;}    
    .wish__product-info--sku{font-size: 11px;}  
    .wish__link-item{font-size: 10px; padding-right: 9px;} 
    
}  
@media screen and (max-width: 400px) {.wish-item-total{flex-basis: 33%;}}
@media screen and (max-width: 350px){
    .wish__block-head, .wish__body-item{column-gap: 20px; } 
    .wish__head-item{font-size: 13.5px;}    
    .wish__product-image{flex-basis: 30%; max-height: 60px; min-height: 50px;}  
    .wish__product-info--title{font-size: 11px; width: 100%; margin: 0;}  
    .wish__product-info--sku{font-size: 10px;}  
    .wish__link-item{font-size: 10px; padding-right: 4px;} 
    .wish-item-total{flex-basis: 35%;}
    
}   
    


