@import '../../styles/scss_common/mixins';

.whoops{
	width: 100vw;	
	background-color: #FCEDEA;
	position: relative;
	z-index: 2;
}


.whoops__body{
	width: 100vw;
	min-height: 600px;
	height: auto;
	background-color: #FFFFFF;
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	column-gap: 30px;
	padding-block: 120px;
	box-sizing: border-box;
}

.whoops__body-img{	
	height: 700px;	
	position: absolute;
	bottom: 0;
	left: -7%;
}

.whoops__body-block{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-self: flex-start;
	row-gap: 25px;	
	width: 45vw;
}

.whoops__body-block--title{
	font-size: 150px;
	line-height: 100%;
	text-transform: capitalize;
	color: #2F3237;
	margin: 0;
	margin-left: -90px;
}

.title-404-span{
	font-size: 40px;
	margin-left: 25px;
}

.whoops__body-block--text{
	font-size: 18px;
	line-height: 100%;
	color: #666666;
	margin: 0;
	margin-top: -40px;
	margin-bottom: 30px;
	margin-left: 30px;
	text-transform: none;
}

.whoops__body-block--link{
	text-decoration: none;
	padding: 15px 20px;
	width: 400px;
	height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 3px solid #FCEDEA;
	box-sizing: border-box;
	font-size: 24px;
	line-height: 100%;
	text-transform: capitalize;
	color: #666666;	
}
.whoops__tracker-red{
    color: #D05278;	
	text-transform: none;
	margin-left: 5px;
}

@media screen and (max-width: 4800px){
	.whoops__body{min-height: 800px; column-gap: 0; padding-block: 180px;}	
	.whoops__body-img{height: 1100px; bottom: 0; left: 17%;}	
	.whoops__body-block{row-gap: 50px; width: 53vw; margin-right: 20px;}	
	.whoops__body-block--title{font-size: 120px; margin-left: 0;}	
	.title-404-span{font-size: 80px; margin-left: 25px;}	
	.whoops__body-block--text{font-size: 40px; margin-top: -30px; margin-bottom: 30px; margin-left: 30px;width: 90%;  overflow: hidden; text-overflow: ellipsis;
        display: -webkit-box; -webkit-line-clamp: 1; -webkit-box-orient: vertical;}	
	.whoops__body-block--link{padding: 15px 20px; width: 22vw; height: 120px; border: 4px solid #FCEDEA; font-size: 48px;}
	.whoops__tracker-red{margin-left: 5px;}
}
