@import '../../../styles/scss_common/mixins';

.registration-page{
	width: 100vw;    
    position: relative;
    background-color: #FCEDEA;
    z-index: 1;
}

.registration-page__body{
	position: relative;
    width: 100vw;
    padding-bottom: 60px;   
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 50px;
    background-color: #FFFFFF;
}

.registration-page__body--decor{
	position: absolute;
    top: 0%;
    left: -18%;
    height: 500px;    
    opacity: 65%;
    z-index: 25;
}

.registration-page__body--decor2{
	position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-10deg);
    opacity: 65%;
    z-index: 25;
}

.registration-page__body-block{
	width: 100vw;
    height: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 80px;  
	padding-block: 80px; 
}

.registration-page__body-block--component{
	width: 100vw;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 5px;
}

@media screen and (max-width: 4800px){
    .registration-page__body{padding-bottom: 0;  row-gap: 50px;}    
    .registration-page__body--decor{top: 22%; left: -11%; height: 900px;}    
    .registration-page__body--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg);}    
    .registration-page__body-block{ height: auto; column-gap: 80px;  padding-bottom: 220px; padding-top: 180px;}    
    .registration-page__body-block--component{row-gap: 5px;}
}
