@import '../../styles/scss_common/mixins';

.gallery{
	width: 100vw;
	background-color: #FFFFFF;	
	padding-bottom: 180px;   
}

.gallery-block{
    width: 100vw;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 80px;    
    z-index: 1;    
}


.gallery-block__content{
    width: 468px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 30px;
    z-index: 20;
}

.gallery-block__content-logo{
    font-size: 50px;
    line-height: 100%;
    margin: 0;
    text-align: center;
    text-transform: capitalize;
    color: #D05278;
    z-index: 20;
    margin: 0;
}

.gallery-block__content-title{
    font-size: 54px;
    line-height: 100%;
    text-align: center;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
}

.gallery-block__content-text{
    font-size: 16px;
    line-height: 170%;
    text-align: center;
    color: #666666;
    margin: 0;
}

.gallery-block__cards{
    width: 95vw;
    height: 460px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 20px;  
    overflow-x: hidden;     
    
}

.gallery-block__cards_btn{
    width: 40px;
    height: 460px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 30;   
}

.gallery-block__btns-btn{
    width: 40px;
    height: 60px;
    background-color: #FAF9FF;
    box-sizing: border-box;
    display: flex;    
    justify-content: center;
    align-items: center;
    padding: 15px 10px;  
    border: none;  
    outline: none;
    font-size: 22px;
    color: #999999;
}
.gallery-block__cards-block{ 
    background-color: #FFFFFF;    
    flex-basis: 95%;
	width: 95%;
    height: 460px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 10px;
    overflow: hidden; 
    padding-inline: 40px; 
    
}
.gallery-block__cards-block::-webkit-scrollbar {
    width: 0;
}
.gallery-block__block-card{    
    height: 460px;   
    background-color: #f9f8f8; 
    display: flex;
    flex-direction: row;   
    justify-content: flex-start;
    align-items: flex-start;
	column-gap: 0px;   
    text-decoration: none;    
	position: relative;
}
.gallery-block__card-img{     
    width: 45%;
	height: 460px;
    position: relative; 
	display: flex;
	justify-content: center;
	align-items: center;
}
.gallery-block__card-image{   
    min-width: 12vw;
    max-width: 35vw; 
    min-height: 200px; 
    max-height: 250px;  	
    object-fit: cover;    
}


.gallery-block__card-info{
	height: 460px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 20px;
	padding: 30px 20px;	
	flex-basis: 55%;
	width: 55%;
	// position: relative;
	box-sizing: border-box;
}

.gallery-block__img-sale{
    position: absolute;
    top: 0px;
    right: 0px;
    width: 83px;
    height: 54px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gallery-block__img-new{
    position: absolute;
    top: 74px;
    right: 0px;
    width: 83px;
    height: 54px;
    background-color: rgba(208, 82, 120, 0.7);
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}

.new-gallery{    
    position: absolute;
    top: 0px;
    right: 0px;   
}

.gallery-block__card-title{
	width: 70%;
    height: 150px;
    font-size: 24px;
    line-height: 100%;   
    color: #222222;
    text-transform: capitalize;
    margin: 0;    
	z-index: 30;
}

.gallery-block__card-text{
	margin: 0;
	font-size: 14px;
	line-height: 130%;
	color: #666666;
	width: 85%;
}
.gallery-block__card-cost{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    margin-top: -10px;
}


.gallery-block__card-old-price{
    font-size: 20px;
    line-height: 170%;
    text-align: center;
    text-decoration: line-through;
    color: #999999;
}
.gallery-block__card-price{
    font-size: 24px;
    line-height: 170%;
    text-align: center;
    color: #666666;
}

