@import '../../styles/scss_common/mixins';

.testimonials-dashboard{
	width: 50vw;
	height: 702px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 20px;
	overflow-y: auto;
	overflow-x: hidden;
	box-sizing: border-box;
	border: 3px solid #FCEDEA;
}

.testimonials-dashboard::-webkit-scrollbar {
    width: 12px; 
	height: 12px;              /* ширина scrollbar */
  }
  .testimonials-dashboard::-webkit-scrollbar-track {
    background: #FCEDEA;        /* цвет дорожки */
  }
  
  .testimonials-dashboard::-webkit-scrollbar-thumb {
    background-color: #FCEDEA;    /* цвет плашки */
    border-radius: 20px;       /* закругления плашки */
    border: 3px solid #d4d2d2;  /* padding вокруг плашки */
	height: 12px;
  }

.testimonials-dashboard__item{
	width: 90%;
	padding: 40px;
	border: 2px solid #FCEDEA;	
	border-left: none;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 26px;
}

.testimonials-dashboard__item-author{
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	column-gap: 30px;
}

.testimonials-dashboard__author-image--block{
	border-radius: 50%;
	width: 40px;
	height: 40px;
	background-color: #EEEEEE;
	position: relative;	
}

.testimonials-dashboard__author-image{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	height: 40px;
	width: 40px;
	border-radius: 50%;
	object-fit: cover;	
}

.testimonials-dashboard__author-name,
.testimonials-dashboard__author-date{
	font-size: 15px;
	line-height: 170%;
	text-transform: capitalize;
	color: #222222;
	margin: 0;
	width: 140px;
}

.testimonials-dashboard__author-name{margin-left: 10px;}

.testimonials-dashboard__author-date{
	font-size: 14px;
	text-transform: none;
	color: #999999;
}
.testimonials-dashboard__author-rating{width: 25%;}

.testimonials-dashboard__item-review{
	width: 90%;
	font-size: 14px;
	line-height: 170%;
	color: #666666;
	margin: 0;

}

@media screen and (max-width: 4800px) {
	.testimonials-dashboard{width: 45vw; height: 1002px; row-gap: 30px; border: 4px solid #FCEDEA;}	
	.testimonials-dashboard::-webkit-scrollbar { width: 25px; height: 10px;}	  
	.testimonials-dashboard::-webkit-scrollbar-thumb {border-radius: 20px; border: 4px solid #d4d2d2; height: 10px;}	
	.testimonials-dashboard__item{width: 100%; padding: 40px; border: 2px solid #FCEDEA; row-gap: 36px;}	
	.testimonials-dashboard__item-author{column-gap: 50px; width: 90%; flex-grow: 1;}	
	.testimonials-dashboard__author-image--block{border-radius: 50%; width: 120px; height: 120px;}
	.testimonials-dashboard__author-image{top: 0; bottom: 0; left: 0; right: 0; height: 120px; width: 120px; border-radius: 50%;}	
	.testimonials-dashboard__author-name, 	.testimonials-dashboard__author-date{font-size: 36px; width: 30%;}	
	.testimonials-dashboard__author-date{font-size: 36px; }
	.testimonials-dashboard__author-rating{width: 20%; font-size: 36px;}	
	.testimonials-dashboard__item-review{width: 90%; font-size: 32px;}	  
}
@media screen and (max-width: 3000px) {	
