	.testimonials-dashboard__author-image{height: 100px; width: 100px; border-radius: 50%;}	
	.testimonials-dashboard__author-name, .testimonials-dashboard__author-date{font-size: 30px; width: 30%;}	
	.testimonials-dashboard__author-date{font-size: 30px; }
	.testimonials-dashboard__author-rating{width: 20%;}	
	.testimonials-dashboard__item-review{width: 90%; font-size: 28px;}	  
}
