@import '../../../styles/scss_common/mixins';

.shop__view{
    width: 100vw;
    z-index: 1;
    background-color: #FFFFFF;
    position: relative;   
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 40px;
    cursor: pointer;
    padding-top: 120px;
}

.shop__view-image--decor{
    position: absolute;
    top: 15%;
    left: -22%;
    height: 600px;   
    opacity: 75%;
}
.shop__view-block{
    width: 80vw;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 60px;
}

.shop__view-block--cards{
    width: 80vw; 
    height: 100%;   
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 80px;
}

.shop__view-cards--trackers{
    width: 23%;   
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 50px;    
}
.shop__view-trackers--search{
    width: 88.6%;
    height: 60px;
    position: relative;
}

.shop__view-trackers--input{
    width: 100%;
    height: 60px;
    outline: none;
    border: 1px solid #FCEDEA;
    padding-left: 30px;
    padding-block: 18px;
    box-sizing: border-box;
}
.shop__view-trackers--input::placeholder{
    font-size: 14px;
    line-height: 170%;
    color: #999999;
}

.shop__view-trackers-icon{
    position: absolute;
    top: 35%;
    right: 8%;
    width: 18px;
    height: 18px;
    fill: #FCEDEA;
}

.shop__view-cards--block{
    width: 76%;    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    row-gap: 30px;   
}

.shop__view-block--form{
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 60px;
}

.shop__view-form--item{
    width: 92px;
    height: 40px;
    border: 1px solid #EEEEEE;
    background-color: #FAF9FF;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 17px;    
    position: relative;
    box-sizing: border-box;
}

.shop__view-item--text{
    font-size: 14px;
    line-height: 170%;    
    z-index: 15;
}

.shop__view-item-image{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 92px;
    height: 40px;
    z-index: 10;
}

.shop__view-block--pagination{
    margin: 0 auto;
    width: 310px;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    column-gap: 5px;
   
}

.shop__view-pagination-item{
    box-sizing: border-box;
    background-color: #EEEEEE;
    border: 1px solid #EEEEEE;
    width: 40px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    outline: none;
}

.shop__view-pagination-button{
    width: 40px;
    height: 100%;
    outline: none;
    border: none;
    background-color: transparent;
    color: #666666;
    font-size: 16px;
    // display: flex;
    justify-content: center;
    align-items: center;
}

.shop-view__notfound{
    width: 100wv;
    min-height: 400px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;  
    row-gap: 30px; 
}

.shop-view__notfound-message{
    font-size: 28px;
    line-height: 100%;
    color: rgba(208, 82, 120, 0.7);   
    letter-spacing: 2px;
}

.shop-view__notfound-navigators{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 30px;
}

.shop-view__notfound-navigator{
    text-decoration: none;
    display: block;
    width: 179px;
    height: 50px;
    background-color: #FCEDEA;
    color: #999999;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

