@import '../../../styles/scss_common/mixins';

.total__full-block-content{
    width: 100%;
    height: 100%;
    padding: 60px;
    box-sizing: border-box;
    background-color: #FAF9FF;  
    display: flex; 
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    row-gap: 30px;
}

.item-goods{border-bottom: 1px solid #EEEEEE;}
.item-discount{border-bottom: 2px solid #222222;}
.item-total{margin-top: 10px;}

.total__block-item{
    width: 100%;    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;  
    padding: 20px;      
    box-sizing: border-box;     
}

.btns-item{
    column-gap: 20px;
    padding: 0;
}

.total__item-title{
    font-size: 16px;
    line-height: 170%;
    color: #222222;
    margin: 0;
}

.total__item-promo--block{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    width: 230px;
    height: 50px;
}

.total__item-promocode--input{
    outline: none;
    border: none;
    color: #666666;
    font-size: 14px;
    padding: 5px 10px;   
    width: 100%;
    height: 100%;
    border: 2px solid #FCEDEA;
    letter-spacing: 0.6px;
}


.total__item-promocode--btn{
    position: absolute;
    top: 0;
    right: 5px;
    height: 100%;
    width: 50px;
    background-color: #FCEDEA;
    border:none;
    outline: none;
    cursor: pointer;
    color: #666666;
    font-size: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.total__item-totalAmount{
    font-size: 18px;
    line-height: 100%;
    text-transform: capitalize;
    color: #666666;
    margin: 0;
}

.total__item-total--item{
    font-size: 24px;
    line-height: 100%;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
}


.total-amount__block-btn{
    width: 100%;
    height: 60px;
    padding-inline: 20px;
    border: none;
    outline: none;
    background-color: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    box-sizing: border-box;      
}

.total__btn-link{
    text-decoration: none;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color:#FFFFFF;  
    padding: 10px 25px;  
}

.total__item-message{
    text-align: center;
    color: #D05278;
    font-size: 16px;
}

@media screen and (max-width: 4800px){
    .total__full-block-content{ width: 100%; height: 100%; padding: 60px 100px; row-gap: 30px; box-sizing: border-box;}    
    .item-goods{border-bottom: 2px solid #EEEEEE;}
    .item-discount{border-bottom: 3px solid #222222;}
    .item-total{margin-top: 20px;}    
    .total__block-item{width: 100%; padding: 20px;}    
    .btns-item{column-gap: 20px;}    
    .total__item-title{font-size: 36px; line-height: 170%;}    
    .total__item-promo--block{width: 50%; height: 80px;}    
    .total__item-promocode--input{font-size: 32px; padding: 5px 20px;  border: 4px solid #FCEDEA; letter-spacing: 0.6px;}    
    .total__item-promocode--btn{top: 0; right: 5px; width: 20%; font-size: 28px;}    
    .total__item-totalAmount{font-size: 36px;}    
    .total__item-total--item{font-size: 48px;}    
    .total-amount__block-btn{width: 90%;  height: 120px;  margin-top: 0;}    
    .total__btn-link{font-size: 32px; padding: 10px 25px;}    
    .total__item-message{font-size: 32px;}
}
@media screen and (max-width: 3000px){
    .total__full-block-content{ padding: 40px; padding-bottom: 50px;}        
    .total__item-title{font-size: 32px;}      
    .total__item-promocode--input{font-size: 28px;  border: 3px solid #FCEDEA;}    
    .total__item-promocode--btn{width: 26%; font-size: 28px;}    
    .total__item-totalAmount{font-size: 32px;}    
    .total__item-total--item{font-size: 40px;}    
    .total-amount__block-btn{height: 100px;}    
    .total__btn-link{font-size: 30px;}    
    .total__item-message{font-size: 30px;}
}
