@import '../../../styles/scss_common/mixins';

.subscription{
    width: 75vw;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; 
    row-gap: 40px;  
    z-index: 1;
    position: relative;    
    padding-top: 40px;
}

.subscription__title-block{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 10%;
}
.subscription__title{
    margin: 0;
    margin-left: 30px;
    color: #D05278;
    font-size: 20px;
    line-height: 130%;    
}

.subscription__title-block--btn{
    width: 179px;
    height: 50px;
    outline: none;
    border: none;
    border-radius: 25px;
    background-color: #FCEDEA;
    color: #666666;
    font-size: 16px;
    text-transform: uppercase;
    box-shadow: 2px 0 2px 2px #FAF9FF;
    cursor: pointer;
}

.subcription__block{
    width: 75vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: #FCEDEA;
    
}

.subscription__touch-content{
    width: 75vw;
    height: 310px;
    position: relative;
    overflow: hidden;
    z-index: 4;
    display: flex; 
    flex-direction: column; 
    justify-content: center; 
    align-items: flex-end;
}

.subscription__touch-content--bg-image{
    position: absolute;
    top: -40px;
    left: -14px;
    width: 524px;
    height: 450px;
    z-index: 16;
}
.subscription__touch-content--img{
    position: absolute;
    top: 8px;
    left: 60px;
    width: 460px;
    height: 302px;
    z-index: 17;
}

.subscription__touch-content--block{
    width: 39vw;
    height: 175px;   
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
}

.subscription__touch-block--title{    
    font-size: 40px;
    line-height: 140%;;
    text-transform: capitalize;
    color: #222222;
    margin: 0;
    z-index: 18;
}

.subscription__touch-block--text{
    margin: 0;
    margin-top: -28px;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    z-index: 18;
}

.subscription__touch-block-subscription{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 10px;
    height: 60px;
    width: 565px;
    z-index: 18;
}
.subscription__touch-subscription--input{
    width: 380px;
    height: 60px;
    border: none;
    outline: none;
    color: #999999;
    padding-block: 18px;
    padding-left: 30px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 170%;  
    z-index: 18;  
}
.subscription__touch-subscription--btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 175px;
    height: 60px;
    background-color: #222222;
    padding: 23px 50px;
    font-style: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;
    z-index: 18;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border: none;
}

