    .subscription__touch-subscription--input{ width: 17vw;  height: 100px; padding-block: 18px;  padding-left: 30px; font-size: 32px;}
    .subscription__touch-subscription--input::placeholder{font-size: 32px;}
    .subscription__touch-subscription--btn{ width: 10vw; height: 100px; padding: 23px 50px; font-size: 30px;}
