@import '../../../styles/scss_common/mixins';

.payment{
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;	
	row-gap: 60px;	
}

.payment__item{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 15px;
}

.payment__item-title{
	font-size: 32px;
	line-height: 130%;
	text-transform: capitalize;
	color: #222222;
	margin: 0;	
}

.payment__item-block{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 15px;
	z-index: 25;
}

.payment__block-item{
	width: 95%;
	min-height: 100px;	
	background-color: #FFFFFF;
	border: 1px solid #EEEEEE;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 25px;
	box-sizing: border-box;
	padding: 30px;	
}

.payment__block-title--block{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	column-gap: 20px;	
}

.payment__block-item--check{
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background-color: #FFFFFF;
	border: 2px solid #999999;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: -10px;
}

.payment__item-check--checked{
	width: 12px;
	height: 12px;
	border-radius: 50%;
	 background-color: #D05278;
}

.payment__block-item--title{
	width: 80%;
	font-size: 20px;
	line-height: 120%;
	text-transform: capitalize;
	color: #222222;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	column-gap: 8px;

}

.payment-indicator{
	width: 16px;
	height: 16px;
	border-radius: 50%;	
	border: 2px solid #D05278;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;	
	color: #D05278;
	text-transform: none;
}

.payment__block-item--form-block{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 30px;
}

.payment__block-item--card{align-self: start;}

.payment__form-input--block{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	column-gap: 30px;
}
.payment__block-item-form{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 30px;	
}

.payment__form-input, .payment__form-input--half{
	outline: none;
	width: 100%;
	height: 60px;
	background-color: #FFFFFF;
	border-radius: 10px;
	border: 2px solid #999999;	
	box-sizing: border-box;
	font-size: 16px;
	line-height: 170%;
	color: #666666;
	letter-spacing: 1.5px;
} 

.payment__form-input{padding: 18px 30px;}

.payment__form-input--half{flex-basis: 50%; padding-inline: 18px 40px;}

.payment__form-btn{
	width: 100%;
	height: 60px;
	background-color: #222222;
	color: #FFFFFF;
	outline: none;
	border: none;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 18px;
	line-height: 120%;
	letter-spacing: 1px;
}

input[type="text"]:focus, input[type="text"]:active, input[type="text"]:-webkit-autofill{background-color: #FFFFFF; color: #999999; outline: none}

.payment__block-item--paypal{
	width: 80%;
	align-self: center;
}

