@import '../../styles/scss_common/mixins';

.wish{
    width: 100vw;
    position: relative;
    z-index: 1;
    background-color: #FCEDEA;
}
.wish__content{
    width: 100vw; 
    min-height: 500px;
    position: relative;  
    background-color: #fff;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 40px;    
    padding-top: 80px;
    z-index: 15;
}

.wish__card-decor{
    position: absolute;
    top: 0%;
    left: -22%;
    height: 600px;    
    opacity: 65%;
    z-index: 25;
}
.wish__card-decor2{
    position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-30deg);
    opacity: 65%;
    z-index: 25;
}

.wish__content-btns{    
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 10px;
}
.wish__content-btns--item{    
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;    
    height: 60px;
    padding: 23px 50px;
    background-color: #FAF9FF;
    border: 1px solid #EEEEEE;
    outline: none;
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #222222;
    box-sizing: border-box;
}
.item-black{
    background-color: #222222;
    border: none;
    color: #FFFFFF;
    outline: none;
}

@media screen and (max-width: 4800px){
    .wish__content{min-height: 800px; row-gap: 40px;padding-block: 180px;}    
    .wish__card-decor{ top: 30%; left: -11%; height: 900px; opacity: 65%;}
    .wish__card-decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}      
    .wish__content-btns{width: 75vw; column-gap: 20px;}    
    .wish__content-btns--item{height: 100px; padding: 23px 50px; border: 2px solid #EEEEEE; font-size: 36px; width: 15vw;}
    .item-black{border: none;}
}
