@import '../../styles/scss_common/mixins';

.trend{
    width: 100vw;    
    background-color: #FFFFFF;
    padding-block: 100px;     
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    row-gap: 60px;    
}

.trend__content{
    width: 33vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 30px;
    z-index: 20;
}

.trend__content-logo{
    font-size: 50px;
    line-height: 100%;
    margin: 0;
    text-align: center;
    text-transform: capitalize;
    color: #D05278;
    z-index: 20;
    margin: 0;
}

.trend__content-title{
    font-size: 54px;
    line-height: 100%;
    text-align: center;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
}

.trend__content-text{
    font-size: 16px;
    line-height: 170%;
    text-align: center;
    color: #666666;
    margin: 0;
    width: 80%;
}

.trend__category-block{    
    min-height: 41px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 15px;
}

.trend__category-block--item{ 
    align-self: center;   
    min-height: 41px;
    background-color: #FAF9FF;
    border: 1px solid #EEEEEE;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px 30px;
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    box-sizing: border-box;
    outline: none;
}

.trend__category-block--item:active, .trend__category-block--item:hover, .trend__category-block--item:focus{
    background-color: #D05278;
    color: #FFFFFF;
}

.trend__cards{
    width: 90vw;       
    overflow: hidden;  
    align-self: center;  
}


.trend__cards-block{ 
    background-color: #FFFFFF; 
    width: auto;     
    min-height: 560px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 40px;
    overflow-y: hidden;    
}
.trend__cards-block::-webkit-scrollbar {
    width: 0;
}
  
.trend__block-card{  
    width: 25vw;
    flex-basis: 25%;
    min-height: 520px; 
    background-color:  #f9f8f8; 
    border: 2px solid #f9f8f8; 
    display: flex;
    flex-direction: column;   
    justify-content: stretch;
    align-items: center;
    padding-inline: 20px;
    padding-bottom: 20px;
    text-decoration: none;
    display: block;
}
.trend__card-img{     
    height: 380px;
    width: 25vw;
    position: relative; 
    display: flex;
    justify-content: center;   
    align-items: center;
    padding-top: 20px;
}
.trend__card-image{   
    max-height: 350px;   
    max-width: 22vw;  
    height: auto;
    width: auto;
}
.trend__card-trackers--together{
    position: absolute;
    top: 0;
    right: 0;
    width: 83px;
    height: 68px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 0;
}
.trend__img-sale{
    position: absolute;
    top: 0;
    right: -20px;
    width: 83px;
    height: 34px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
}
.new-trend-position{
    position: absolute;
    background-color: rgba(208, 82, 120, 0.7);    
    width: 83px;
    height: 34px;
}
.new-together{
    background-color: rgba(208, 82, 120, 0.7);
    position: absolute;
    top: 34px;
    right: -20px;
    width: 83px;
    height: 34px;
}
.trend__card-title{
    height: 40px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #222222;
    text-transform: capitalize;
    margin: 0;
    margin-top: 20px;
}
.trend__card-cost{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
    margin-top: 9px;
}

.trend__card-old-price{
    font-size: 16px;
    line-height: 170%;
    text-align: center;
    text-decoration: line-through;
    color: #999999;
}
.trend__card-price{
    font-size: 18px;
    line-height: 170%;
    text-align: center;
    color: #666666;
}

.trends__cards-arrows{
    width: 100%;   
    align-self: center;    
    height: 200px;
    position: relative;
    margin-top: -40px;
}

.trends__cards-arrow--left, .trends__cards-arrow--right{
    font-size: 56px;
    color: #f7d4ce;
    position: absolute;
    top: 10%;
}

.trends__cards-arrow--left{ left: 3%;}
.trends__cards-arrow--right{right: 5%;}


