@import '../../../styles/scss_common/mixins';

.basket-component{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 30px;	
}

.basket-component__title{
	width: 100%;	
	padding-bottom: 15px;
	border-bottom: 2px solid #222222;
	font-size: 32px;
	line-height: 130%;
	text-transform: capitalize;
	color: #222222;
	margin: 0;
}

.basket-component__block{
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 10px;
}

.basket-component__block-item{
	width: 100%;
	height: 110px;
	background-color: #FFFFFF;
	border: 2px solid #FCEDEA;
	padding: 20px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	column-gap: 20px;
}

.basket-component__item-image{
	max-width: 70px;
	min-width: 50px;
	min-height: 50px;
	max-height: 70px;
	object-fit: cover;
}

.basket-component__item-info{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 10px;
}

.basket-component__info-title{
	font-size: 20px;
	line-height: 120%;
	text-transform: capitalize;
	color: #000000;
	margin: 0;
}

.basket-component__info-title span {font-size: 16px; line-height: 100%; color: #999999; text-transform: none; margin-left: 10px;}

.basket-component__info-price{
	margin: 0;
	font-size: 14px;
	line-height: 100%;
	text-transform: capitalize;
	color: #222222;
}

.basket-component__info-sku{
	font-size: 10px;
	line-height: 170%;
	color: #666666;
	margin: 0;
}

@media screen and (max-width: 4800px){
	.basket-component{row-gap: 60px;}	
	.basket-component__title{padding-bottom: 20px; border-bottom: 3px solid #222222; font-size: 60px; line-height: 130%;}	
	.basket-component__block{row-gap: 40px;}	
	.basket-component__block-item{height: auto; border: 3px solid #FCEDEA; padding: 40px; column-gap: 60px;}	
	.basket-component__item-image{ min-height: 100px; max-height: 300px; min-width: 3vw; max-width: 17vw; flex-basis: 15%;}	
	.basket-component__item-info{row-gap: 20px; flex-basis: 70%;}	
	.basket-component__info-title{font-size: 40px; line-height: 120%;}	
	.basket-component__info-title span {font-size: 32px; margin-left: 10px;}	
	.basket-component__info-price{font-size: 36px;}	
	.basket-component__info-sku{font-size: 24px; line-height: 170%;}
}
@media screen and (max-width: 3500px){
	.basket-component{row-gap: 60px;}	
	.basket-component__title{font-size: 50px;}
	.basket-component__item-info{ flex-basis: 75%;}	
	.basket-component__info-title{font-size: 36px;}	
	.basket-component__info-title span {font-size: 32px;}	
	.basket-component__item-image{ min-height: 100px; max-height: 300px; min-width: 3vw; max-width: 27vw; flex-basis: 20%;}
}
