@import '../../../styles/scss_common/mixins';

.profile-component__myinfo{    
    width: 80vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;    
}

.profile-component__body{    
    width: 80vw;   
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 20px;
    position: relative;
}

.profile-component__body-save--information{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 40px;
}

.profile-component__body-title{
    color: #666666;
    font-size: 24px;
    flex-basis: 50%;
}

.profile-component__body-button--save{
    position: relative;	
	cursor: pointer;
	outline: none;    
	text-decoration: none;
	font-size: 24px;    
    width: 50%;
	height: 60px;	
    display: flex;
    justify-content: center;
    align-items: center;    
    box-sizing: border-box;  
    word-spacing: 2px;  
    background-color: transparent; 
    border: 2px solid #D05278; 
    color: #D05278;
    padding: 10px; 
    border-radius: 10px;   
}

.profile-component__body-message{
    color: #a48c8a;
    font-size: 18px;
    font-style: italic;
    margin: 0;
    margin-bottom: 10px;    
    width: 55%;
    align-self: end;
}

.profile-component__body-item{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
}

.profile-image{    
    align-self: flex-start;
    width: 100px;
    height: 100px;    
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 20px;
    z-index: 100;
}

.profile-component__item-avatar--block{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 25px;
    z-index: 100;
}

.profile-component__item-image--block{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #EEEEEE;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 15px;
    position: relative;
}

.profile-component__item-image{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    position: relative;
    border: 5px solid #FCEDEA;   
}

.profile-component__item-button{
    position: absolute;
    top: -4px;
    right: -4px;
    width: 30px;
    height: 30px;
    background-color: #FCEDEA;
    border-radius: 50%;
    border: 1px solid #666666;    
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666666;    
    font-size: 10px;
    font-weight: 500;
 }

 .profile-component__item-label{
    position: relative;
	display: inline-block;    
 }

 .profile-component__item-inputFile{
    position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;  
    z-index: 100;  
 }

 .input-file-btn {
	position: relative;	
	cursor: pointer;
	outline: none;
	text-decoration: none;		
    font-size: 22px;
    line-height: 100%;    
    color: #666666;
    flex-basis: 50%;	
    min-width: 170px;
    display: flex;  
    padding: 10px;  
    border: 2px solid #EEEEEE;
    background-color: #FAF9FF;
}

.input-file-text {
	padding: 0 10px;
	line-height: 40px;
	display: inline-block;
}

.profile-component__item-title{        
    font-size: 22px;
    line-height: 100%;    
    color: #666666;
    flex-basis: 50%;
}

.profile-component__title-name{
    margin-left: 10px;
    text-transform: capitalize;
    font-size: 21px;
    text-decoration: none;
    color: #D05278;
    font-style: italic;
}

.title-email{text-transform: none;}

.profile-component__item-input{    
    height: 60px;
    width: 250px;
    padding: 5px 10px;
    box-sizing: border-box;
    background-color: #FAF9FF;
    color: #666666;
    font-style: 22px;
    line-height: 100%;
    text-align: center;
    border: none;
    outline: none;
    flex-basis: 25%;   
}

.long-input{font-size: 18px; border: none; outline: none;}
.long-input li, .long-input option, .profile__component-option{
    max-width: 5vw;    
    background-color: #FCEDEA;
    outline: none;
    border: none;
    font-size: 18px;     
    line-height: 50px;
}
.profile__component-option:hover, .long-input li:hover, .long-input option:hover{background-color: #FCEDEA; color: #666666;}
.long-input ul{ height: 100px; overflow-y: auto; }
.long-input ul::-webkit-scrollbar{width: 0;}

.profile-component__item-btn{
    width: 200px;
    height: 60px;
    box-sizing: border-box;
    background-color: #FCEDEA;
    border: none;
    outline: none;
    color: #999999;
    font-size: 22px;
    line-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 25%;
}

.profile-adaptive-btn{display: none; z-index: 45;}
.profile-adaptive-btn--img{display: none; z-index: 40;}

