@import '../../../styles/scss_common/_mixins';

.home{
    width: 100vw;      
    position: relative;
    overflow-x: hidden;
    box-sizing: border-box;
    background-color: #FCEDEA;    
}

.intro{
    width: 100vw;    
    height: 535px;    
    position: relative;
}

.intro__decor{
    height: 650px;    
    position: absolute;
    top: -115px;
    right: 5%;
    z-index: 10;
}


.intro__background-effect{
    position: absolute;
    left: -15%;
    top: 38%;
    height: 800px;
    opacity: 75%;
    transform: rotate(-25deg);
    z-index: 25;
}

.intro__content{
    padding-top: 80px;
    padding-left: 282px;
    width: 690px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px; 
    z-index: 15;   
}

.intro__content-logo{
    font-size: 60px;
    line-height: 100%;
    color: #D05278;
    margin: 0;
    z-index: 15;
}

.intro__content-title{
    font-size: 100px;
    line-height: 100%;
    color: #222222;
    margin: 0;
    margin-top: -15px;   
    margin-left: -5px; 
    z-index: 15;
}

.intro__content-text{
    width: 456px;
    font-size: 20px;
    line-height: 150%;
    color: #666666;
    margin: 0;
    z-index: 15;
}

.intro__content-btn{
    width: 179px;
    height: 60px;
    padding: 23px;
    background-color: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 15;
    outline: none;
    border: none;
    
}

.intro__btn-link{
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-top: 30px;
    z-index: 15;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 20px;

}

@media screen and (max-width: 4800px) {
    .intro__decor{
        height: 830px;        
        top: -300px;
        right: 25%;       
    }
    .intro__background-effect{       
        left: -9%;  
        height: 1100px; 
        top: 5%;     
    }

    .intro__content{
        padding-top: 20px;
        padding-left: 682px;           
    }
    
    .intro__content-logo{
        font-size: 80px;        
    }
    .intro__content-text{
        font-size: 30px;
        width: 80%;
    }
    .intro__content-btn{ width: 320px; height: 80px; padding-block: 25px; margin-top: 20px;}
    .intro__btn-link{
        font-size: 24px;
    } 
}
@media screen and (max-width: 3000px) {
