@import '../../../styles/scss_common/mixins';

.contact__connection{
	width: 100vw;
	padding-block: 180px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	background-color: #FFFFFF;
}

.contact__connection-block{
	width: 70vw;	
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	column-gap: 98px;
}

.contact__connection-info{
	flex-basis: 70%;	
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 30px;
}

.contact__connection-info--title{
	margin: 0;
	font-size: 32px;
	line-height: 130%;
	text-transform: capitalize;
	color: #222222;
}


.contact__connection-info--text{
	margin: 0;
	font-size: 16px;
	line-height: 170%;
	color: #666666;
}

.contact__connection-social{
	width: 210px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	row-gap: 5px;
}

.contact__connection-social--title{
	margin: 0;
	font-size: 16px;
	line-height: 170%;
	color: #222222;
}

.contact__connection-social--links{
	width: 210px;
	height: 45px;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
	column-gap: 10px;
}

.contact__links-item--link{
	display: block;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.contact__links-block{
	width: 45px;
	height: 45px;
	background-color: #FAF9FF;
	border: 1px solid #EEE;
	display: flex;
	justify-content: center;
	align-items: center;
}

.contact__links-block--img{
	width: 16px;
	height: auto;
}

.contact-message__form-message{margin-block: 20px;}


@media screen  and (max-width: 4800px){
	.contact__connection{padding-top: 220px; padding-bottom: 120px;}
	.contact__connection-block{column-gap: 130px;}
	.contact__connection-info{row-gap: 50px; flex-basis: 60%;}
	.contact__connection-info--title{font-size: 48px;}
	.contact__connection-info--text{font-size: 32px; line-height: 200%;}
	.contact__connection-social{flex-basis: 30%; width: 30%; row-gap: 50px; align-items: center;}
	.contact__connection-social--title{font-size: 40px;}
	.contact__connection-social--links{width: 100%; column-gap: 20px; height: 100px; justify-content: center;}
	.contact__links-block{width: 100px; height: 100px;}
	.contact__links-block--img{width: 32px;}
}
@media screen  and (max-width: 2500px){
	.contact__connection{padding-block: 140px;}
	.contact__connection-block{column-gap: 150px;}
	.contact__connection-info{row-gap: 50px; flex-basis: 65%;}
	.contact__connection-info--title{font-size: 34px;}
	.contact__connection-info--text{font-size: 24px; line-height: 200%;}
	.contact__connection-social{flex-basis: 30%; width: 30%; row-gap: 30px; align-items: center;}
	.contact__connection-social--title{font-size: 30px;}
	.contact__connection-social--links{width: 100%; column-gap: 15px; height: 80px; justify-content: center;}
	.contact__links-block{width: 80px; height: 80px;}
	.contact__links-block--img{width: 24px;}
}
@media screen  and (max-width: 1800px){
	.contact__connection{padding-block: 100px; padding-bottom: 120px;}	
	.contact__connection-info--title{font-size: 28px;}
	.contact__connection-info--text{font-size: 20px; }
	.contact__connection-social{ gap: 30px; align-items: center;}
	.contact__connection-social--title{font-size: 24px;}
	.contact__connection-social--links{width: 100%; height: 70px; justify-content: center;}
	.contact__links-block{width: 70px; height: 70px;}
	.contact__links-block--img{width: 22px;}
}
@media screen  and (max-width: 1200px){	
	.contact__connection-block{column-gap: 150px; flex-direction: column; row-gap: 60px;}
	.contact__connection-info{row-gap: 50px; flex-basis: 100%;}
	.contact__connection-info--title{font-size: 30px;}
	.contact__connection-info--text{font-size: 18px;}
	.contact__connection-social{flex-basis: 100%; width: 100%; row-gap: 30px; flex-direction: row; padding-bottom: 50px; border-bottom: 2px solid #FAF9FF;}
	.contact__connection-social--title{font-size: 28px; flex-basis: 30%;}
	.contact__connection-social--links{width: 60%; column-gap: 10px; height: 60px; justify-content: flex-start;}
	.contact__links-block{width: 60px; height: 60px;}
	.contact__links-block--img{width: 22px;}
}
@include adaptive(laptop){
	.contact__connection{padding-block: 100px;}
	.contact__connection-info{row-gap: 40px;}
	.contact__connection-info--title{font-size: 26px;}	
	.contact__connection-social--links{column-gap: 7px;}
	.contact__connection-social--title{font-size: 22px; flex-basis: 30%;}
}
@include adaptive(tablet){
	.contact__connection{padding-block: 90px;}
	.contact__connection-info--title{font-size: 26px;}
	.contact__connection-info--text{font-size: 17px; line-height: 200%;}
	.contact__connection-social--title{font-size: 20px; flex-basis: 30%;}
	.contact__connection-social--links{width: 60%; column-gap: 5px; height: 50px; justify-content: flex-start;}
	.contact__links-block{width: 50px; height: 50px;}
	.contact__links-block--img{width: 18px;}
}
@include adaptive(phone){
	.contact__connection{padding-top: 80px; padding-bottom: 20px;}
	.contact__connection-info--title{font-size: 24px;}
	.contact__connection-info--text{font-size: 16px; line-height: 200%;}
	.contact__connection-social{gap: 20px;}
	.contact__connection-social--title{font-size: 19px; flex-basis: 40%;}
	.contact__connection-social--links{width: 55%; column-gap: 5px; height: 40px; justify-content: flex-start;}
	.contact__links-block{width: 40px; height: 40px;}
	.contact__links-block--img{width: 16px;}
}
@media screen  and (max-width: 450px){
	.contact__connection{padding-top: 70px; padding-bottom: 0;}
	.contact__connection-info{row-gap: 35px;}
	.contact__connection-info--title{font-size: 20px;}
	.contact__connection-info--text{font-size: 14px; line-height: 200%;}
	.contact__connection-social--title{font-size: 16px; flex-basis: 45%;}
	.contact__connection-social--links{width: 50%; gap: 10px; height: 90px; justify-content: flex-start; flex-wrap: wrap;}
	.contact__links-block{width: 40px; height: 40px; flex-basis: 35%;}
	.contact__links-block--img{width: 14px;}
}

