@import '../../../styles/scss_common/mixins';

.shop-price{
	display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 39px;
    width: 88.5%;
    padding-top: 60px;    
}
.shop-price-title{
	margin: 0;
    font-size: 24px;
    line-height: 100%;
    color: #222222;
    text-transform: capitalize;
    height: 41px;
    width: 100%;
    vertical-align: middle;
    border-bottom: 2px solid #222222;
    margin-bottom: 15px;
}
.shop-price__indicator{
    width: 100%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 15px;   
    color: #444444;
    padding: 10px;
    padding-bottom: 20px;    
    clip-path: polygon(0 0, 100% 0, 100% 50%, 50% 100%, 0 50%);   
    background-color: #FCEDEA; 
    box-sizing: border-box;
    font-size: 18px;    
}
.shop-price__container{
    position: relative;
    width: 100%;
}
.shop-price__slider{
    width: 100%;
    height: 3px;
    position: absolute;
    margin: auto;
    top: 0;    
    border-radius: 5px;
    background-color: #EEEEEE;
    z-index: 5;
}
.shop-price__slider-indicator{    
    height: 3px;
    position: absolute;   
    top: 0;        
    border-radius: 5px;
    background-color: #FCEDEA;
    z-index: 30;
}
.shop-price__range-input{
	height: 3px;
	width: 232px;    
	background-color: transparent;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    appearance: none;   
    -webkit-appearance: none;
    -moz-appearance: none;   
    outline: none;    
    margin: auto;  
    pointer-events: none;
}
.shop-price__range-input::-webkit-slider-runnable-track{
    -webkit-appearance: none;
    height: 5px;
}
.shop-price__range-input::-moz-range-track{
    -moz-appearance: none;
    height: 5px;
}
.shop-price__range-input::-ms-track{
    appearance: none;
    height: 5px;
}
#slide-1::-webkit-slider-thumb, #slide-1::-moz-range-thumb, #slide-1::-ms-thumb{
    margin-left: -30px; z-index: 10;
}
#slide-2::-webkit-slider-thumb, #slide-2::-moz-range-thumb, #slide-2::-ms-thumb{
    margin-right: -30px; z-index: 10;
}
.shop-price__range-input::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 25px;
    width: 15px;    
    cursor: pointer;
    margin-top: -7px;    
    pointer-events: auto;
    border-radius: 20%; 
    background-color: #FCEDEA;   
    border: 2px solid #666666;
}
.shop-price__range-input::-moz-range-thumb{
    appearance: none;
    height: 25px;
    width: 15px;
    cursor: pointer;
    margin-top: -7px;    
    pointer-events: auto;
    border-radius: 20%;
    background-color: #FCEDEA;    
    border: 2px solid #666666;
    
}
.shop-price__range-input::-ms-thumb{
    appearance: none;
    height: 25px;
    width: 15px;
    cursor: pointer;
    margin-top: -7px;   
    pointer-events: auto;    
    border-radius: 20%;
    background-color: #FCEDEA;    
    border: 2px solid #666666;
}
.shop-price__range-input:active::-webkit-slider-thumb{
    background-color: #ffffff;
    border: 3px solid #FCEDEA;
}

@media screen and (max-width: 4800px){
    .shop-price{row-gap: 90px; width: 88.5%; padding-top: 60px;}    
    .shop-price-title{font-size: 46px; height: auto; border-bottom: 2px solid #222222; padding-bottom: 15px;}    
    .shop-price__indicator{width: 90%; height: 120px; column-gap: 15px; padding: 10px; padding-bottom: 20px; font-size: 36px;}    
    .shop-price__slider{height: 12px;}   
    .shop-price__slider-indicator{ height: 12px; }    
    .shop-price__range-input{ height: 6px; width: 100%;}
    .shop-price__range-input::-webkit-slider-runnable-track{height: 5px;}    
    .shop-price__range-input::-moz-range-track{height: 5px;}
    .shop-price__range-input::-ms-track{height: 5px;}
    #slide-1::-webkit-slider-thumb, #slide-1::-moz-range-thumb, #slide-1::-ms-thumb{margin-left: -30px;}
    #slide-2::-webkit-slider-thumb, #slide-2::-moz-range-thumb, #slide-2::-ms-thumb{margin-right: -30px;}
    .shop-price__range-input::-webkit-slider-thumb{height: 40px; width: 30px; margin-top: -7px; border: 2px solid #666666;}
    .shop-price__range-input::-moz-range-thumb{height: 40px;  width: 30px; margin-top: -7px; }
    .shop-price__range-input::-ms-thumb{height: 40px; width: 30px; margin-top: -7px; border: 2px solid #666666;}
    .shop-price__range-input:active::-webkit-slider-thumb{ border: 3px solid #FCEDEA;}
    .shop-price__indicator-label{font-size: 36px;}
}
