@import '../../../styles/scss_common/mixins';

.profile-component{
    width: 100vw;    
    margin: 0 auto;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 30px;      
}

.profile-component__trackers{
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 15px; 
}

.profile-component__trackers-btn{
    height: 50px;
    width: 130px;
    display: flex;
    justify-content: center;
    align-items: center;   
    border: none;    
    outline: none;
    font-size: 16px;
    line-height: 170%;
    text-decoration: none;
}

@media screen and (max-width: 4800px){    
    .profile-component__trackers{ column-gap: 30px; width: 80vw;}    
    .profile-component__trackers-btn{ height: 100px; width: auto; font-size: 40px; padding: 10px 30px; min-width: 260px;}    
}
@media screen and (max-width: 3500px){     
    .profile-component__trackers-btn{ height: 90px; width: auto; font-size: 36px; padding: 10px 30px; min-width: 260px;}    
}
@media screen and (max-width: 3000px){     
    .profile-component__trackers-btn{min-width: 220px;}    
}
@media screen and (max-width: 2500px){     
    .profile-component__trackers-btn{ height: 80px; width: auto; font-size: 32px; padding: 10px 30px; min-width: 200px;}  
}
@media screen and (max-width: 2000px){  
    .profile-component__trackers{width: 80vw;}       
    .profile-component__trackers-btn{ height: 65px; width: auto; font-size: 28px; padding: 10px 25px; min-width: 170px;}  
}
@media screen and (max-width: 1700px){
    .profile-component__trackers{ column-gap: 20px;}        
    .profile-component__trackers-btn{ height: 60px; font-size: 26px; padding: 10px 20px;}  
}
@media screen and (max-width: 1560px){
    .profile-component__trackers{ column-gap: 20px;}        
    .profile-component__trackers-btn{ height: 50px; font-size: 24px; padding: 5px 20px; min-width: 130px;}  
}
@media screen and (max-width: 1260px){
    .profile-component__trackers{ column-gap: 15px;}        
    .profile-component__trackers-btn{ height: 40px; font-size: 22px; padding: 5px 20px; min-width: 110px;}  
}
@include adaptive(laptop){
    .profile-component__trackers{ column-gap: 10px; width: 85vw;}    
    .profile-component__trackers-btn{ height: 35px; font-size: 16px; padding: 5px 10px; min-width: 90px;}  
}
@include adaptive(tablet){
    .profile-component__trackers{ column-gap: 20px; width: 85vw; flex-wrap: wrap; row-gap: 20px; justify-content: center; align-items: center;}    
    .profile-component__trackers-btn{ height: 30px; font-size: 14px; padding: 5px 10px; flex-basis: 42%;}  
}
@include adaptive(phone){
    .profile-component__trackers{ width: 88vw;}    
    .profile-component__trackers-btn{ font-size: 14px;}  
}
@media screen and (max-width: 450px) {
    .profile-component__trackers-btn{font-size: 14px; flex-basis: 40%;}  
}
@media screen and (max-width: 350px) {
    .profile-component__trackers-btn{flex-basis: 38%; min-width: 100px;font-size: 14px;}  
}






