@import '../../../styles/scss_common/mixins';

.profile-subscription{
	width: 100vw;    
    position: relative;
    background-color: #FCEDEA;    
    z-index: 1;
}

.profile-subscription__body{
	position: relative;
    width: 100vw;
    padding-bottom: 60px;   
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 50px;
    background-color: #FFFFFF;
}

.profile-subscription--links{margin-left: -10vw;}

.profile-subscription--decor{
	position: absolute;
    top: 0%;
    left: -18%;
    height: 500px;    
    opacity: 65%;
    z-index: 25;
}

.profile-subscription--decor2{
	position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-10deg);
    opacity: 65%;
    z-index: 25;
}

.profile-subscription-block{
	width: 75vw;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;  
    padding-top: 60px;  
}
.profile-subscription__addition{
    width: 75vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    column-gap: 30px;    
    padding-block: 80px;
}

@media screen and (max-width: 4800px){
    .profile-subscription__body{ padding-bottom: 220px;  row-gap: 120px;}    
    .profile-subscription--decor{top: 30%; left: -11%; height: 900px; opacity: 65%;}     
    .profile-subscription--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}      
    .profile-subscription-block{width: 75vw; row-gap: 180px; padding-top: 180px;}
    .profile-subscription__addition{width: 75vw;  padding-block: 80px;}
    .profile-subscription--links{margin-left: -10vw;} 
}
