@import '../../../styles/scss_common/mixins';

.profile-orders{
	width: 100vw;    
    position: relative;
    background-color: #FCEDEA;    
    z-index: 1;
    
}

.profile-orders__body{
	position: relative;
    width: 100vw; 
    min-height: 800px;    
    z-index: 11;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 50px;
    background-color: #FFFFFF;
   
}

.profile-orders--decor{
	position: absolute;
    top: 0%;
    left: -18%;
    height: 500px;    
    opacity: 65%;
    z-index: 25;
}

.profile-orders--decor2{
	position: absolute;
    bottom: 0%;
    right: -6%;
    height: 400px;
    transform: rotate(-10deg);
    opacity: 65%;
    z-index: 25;
}

.profile-orders-block{
	width: 75vw;   
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;  
    padding-top: 60px;        
}

.profile-orders-components{
	width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 65px;
}

.profile-orders-components-gallery{
	align-self: flex-start;
    width: 100vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 5px;	
}

@media screen and (max-width: 4800px){
    .profile-orders__body{min-height: 800px;  row-gap: 50px; padding-bottom: 120px;}    
    .profile-orders--decor{top: 30%; left: -11%; height: 900px; opacity: 65%;}      
    .profile-orders--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}     
    .profile-orders-block{ width: 80vw; row-gap: 120px;  padding-top: 180px;}    
    .profile-orders-components{ width: 100vw; row-gap: 130px;}    
    .profile-orders-components-gallery{row-gap: 5px; padding-top: 80px;}
}

@media screen and (max-width: 4200px){
    .profile-orders--decor{left: -12%;} 
}
@media screen and (max-width: 3850px){
    .profile-orders--decor{left: -13%;}    
}
@media screen and (max-width: 3600px){
    .profile-orders--decor{left: -14%;}   
    .profile-orders-components-gallery{padding-top: 80px;}   
}

@media screen and (max-width: 3500px){
    .profile-orders--decor{left: -14%; top: 25%; height: 800px;}        
}
@media screen and (max-width: 3000px){
    .profile-orders--decor{ top: 30%; left: -15%; height: 800px; opacity: 65%;}
    .profile-orders--decor2{bottom: 0%; right: -2%; height: 500px; transform: rotate(0deg); opacity: 65%;}  
    .profile-orders--decor-components-gallery{padding-top: 80px;} 
