@import '../../../styles/scss_common/mixins';

.order-delivery{
    width: 100vw; 
    min-height: 400px;   
    z-index: 11;
    background-color: #FFFFFF;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 60px;
    padding-top: 280px;
    padding-bottom: 180px;   
}

.order-delivery__content{
    width: 70vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;   
    position: relative;
}

.center-content{align-self: center; align-items: center;}
.center-content-special{align-self: center; align-items: center; padding: 60px; border-radius: 25px; border: 3px solid #D05278; width: auto; margin-block: 40px;  position: relative;}

.order__delivery-content--block{
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 60px;
}

.order-delivery-block--container{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 60px;
}

.full-page-container{min-height: 600px;}

.order__delivery-block--btns{
    width: 310px;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 6px;
    margin-top: 60px;
    align-self: flex-start;
}

.order__delivery-block--btn{
    width: 152px;
    height: 60px;
    background-color: #FAF9FF;
    border: 1px solid #EEEEEE;
    display: flex;
    justify-content: center;
    align-items: center;   
    font-size: 14px;
    line-height: 100%;
    text-transform: uppercase;
    color: #222222;
    text-decoration: none;
}

.order__delivery-block--btn span {margin-inline: 10px;}

.order__delivery-block-title{
    font-size: 32px;
    line-height: 130%;
    text-transform: capitalize;
    color: #222222;   
    margin: 0;
}

.order__delivery-block-text{
    font-size: 16px;
    line-height: 170%;
    color: #666666;
    width: 100%;
    margin: 0;
    margin-top: -30px;
}

.order__delivery-block-table{
    width: 100%;
    border-collapse: collapse;
    border: 2px solid #FAF9FF;
    
}

.order__delivery-table--td{
    padding-left: 30px;
    font-size: 16px;
    line-height: 170%;
    color: #222222;
}

.order__delivery-table--td--light{
    font-size: 16px;
    line-height: 170%;
    color: #999999;
}

.order__delivery-table--tr {
    background-color: #FFFFFF;
    height: 40px;    
}

.order__delivery-table--tr:nth-of-type(odd){
    background-color: #FAF9FF;
}

.title-center{text-align: center;}
.title-start{align-self: flex-start; margin-left: 60px;}
.order__delivery-payment--message{
    line-height: 140%;
    color: #D05278;
    font-size: 20px;
    margin: 0;
    width: 100%;
    text-align: left;
    margin-left: 20px;

}

@media screen and (max-width: 4800px){
    .order-delivery{min-height: 1200px; row-gap: 90px; padding-top: 280px; padding-bottom: 280px;}    
    .order-delivery__content{width: 70vw; row-gap: 120px;}
    .center-content-special{padding: 60px; border-radius: 25px; border: 3px solid #D05278; margin-block: 40px;}    
    .order__delivery-content--block{width: 70vw; column-gap: 100px;}    
    .order-delivery-block--container{width: 50%; row-gap: 120px;}    
    .full-page-container{min-height: 800px;}    
    .order__delivery-block--btns{width: 20vw; height: 120px; column-gap: 12px; margin-top: 60px;}    
    .order__delivery-block--btn{width: 50%; height: 120px; border: 2px solid #EEEEEE; font-size: 32px;}    
    .order__delivery-block--btn span {margin-inline: 20px;} 
    .order__delivery-payment--message{font-size: 30px;}

    .order__delivery-block-title{font-size: 60px; margin-left: 20px;}    
    .order__delivery-block-text{font-size: 36px; margin-top: -30px; margin-left: 20px;}    
    .order__delivery-block-table{border: 3px solid #FAF9FF; width: 90%; margin-left: 20px;}    
    .order__delivery-table--td{padding-left: 30px; font-size: 36px;border-right: 3px solid #FAF9FF;}    
    .order__delivery-table--td--light{font-size: 36px; text-align: center;}    
    .order__delivery-table--tr {height: 120px;}
    .title-start{margin-left: 60px;}      
}
