@import '../../../styles/scss_common/mixins';

.shop__view-shop{
    width: 100%;    
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 30px;
    row-gap: 50px;
    flex-wrap: wrap;
    
}
.shop__view-shop--card{
    flex-basis: 32%;
    width: 32%;
    height: 400px;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 10px;    
    padding: 10px;
    box-sizing: border-box;
    position: relative;
    text-decoration: none;
    background-color: #FAF9FF;
}
.shop__view-card--trackers{
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 0;

}
.shop__view-trackers--sale, .shop__view-trackers--new{
    width: 60px;
    height: 40px;
    background-color: #A3D2A2;
    padding: 10px 25px;
    font-size: 20px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}
.shop__view-trackers--new{
    background-color: rgba(208, 82, 120, 0.7);
}
.shop__view-card--image{
        height: 200px;        
        display: flex;
        justify-content: center;
        align-items: center;
}
.shop__view-card--img{
    max-height: 200px;
    min-height: 150px;    
    min-width: 10vw;
    max-width: 28vw;
    object-fit: cover;
}
.shop__view-card--info{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    row-gap: 9px;
    width: 100%;
    padding: 20px;       
}
.shop__view-info--title{   
    font-size: 16px;
    line-height: 120%;
    text-align: center;
    text-transform: capitalize;
    color: #222222;
    height: 60px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-brand{
    color: #D05278;
    margin: 0;
    margin-top: -5px;
    height: 30px;
}
.shop__view-price--block{ 
    width: 100%;    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 10px;
}
.shop__view-price--old{
    font-size: 16px;
    line-height: 120%;   
    text-decoration: line-through;
    color: #999999;   
    margin: 0;
}
.shop__view-price{
    font-size: 18px;
    line-height: 100%;    
    text-transform: capitalize;
    color: #666666;
    margin: 0;
}
@media screen and (max-width: 4800px){
    .shop__view-shop{width: 100%;  column-gap: 30px; row-gap: 60px;}
    .shop__view-shop--card{flex-basis: 32%; width: 32%; height: 700px; row-gap: 10px; padding: 30px; }
    .shop__view-card--trackers{width: 123px;  height: 128px;}    
    .shop__view-trackers--sale, .shop__view-trackers--new{ width: 123px; height: 64px; padding: 10px 25px; font-size: 28px; }
    .shop__view-card--image{ height: 300px;}    
    .shop__view-card--img{min-width: 5vw; max-width: 28vw; min-height: 200px; max-height: 280px;  margin-top: 20px;} 
    .shop__view-card--info{row-gap: 30px; width: 100%; padding: 20px; height: 400px;}    
    .shop__view-info--title{font-size: 36px; line-height: 120%; height: 100px; width: 85%;}    
    .card-brand{margin-top: -5px; height: 38px; font-size: 36px;}    
    .shop__view-price--block{ width: 100%; column-gap: 20px; margin-top: 20px;}    
    .shop__view-price--old{font-size: 36px; line-height: 120%;}    
    .shop__view-price{ font-size: 52px;}
}
@media screen and (max-width: 3500px){    
    .shop__view-info--title{font-size: 32px; height: 110px;}    
    .card-brand{margin-top: -5px; height: 35px; font-size: 32px;}       
    .shop__view-price--old{font-size: 32px;}    
    .shop__view-price{ font-size: 46px;}
}
@media screen and (max-width: 3000px){    
    .shop__view-shop--card{flex-basis: 31%; width: 31%; height: 700px; row-gap: 10px; padding: 30px; }    
    .shop__view-card--info{row-gap: 20px;}    
    .shop__view-info--title{font-size: 30px; line-height: 140%; height: 150px; width: 85%;}    
    .card-brand{margin-top: -5px; height: 32px; font-size: 30px;}      
    .shop__view-price--old{font-size: 32px; line-height: 120%;}    
    .shop__view-price{ font-size: 44px;}
}
@media screen and (max-width: 2500px){
