    .shop__view-card--image{ height: 240px;}    
    .shop__view-card--img{min-width: 5vw; max-width: 28vw; min-height: 180px; max-height: 220px;  margin-top: 20px;} 
    .shop__view-card--info{row-gap: 20px; width: 100%; padding: 20px; height: 360px;}    
    .shop__view-info--title{font-size: 26px; line-height: 140%; height: 130px; width: 85%;}    
    .card-brand{margin-top: -5px; height: 28px; font-size: 26px;}    
    .shop__view-price--block{ width: 100%; column-gap: 15px; margin-top: 20px;}    
    .shop__view-price--old{font-size: 28px; line-height: 120%;}    
    .shop__view-price{ font-size: 38px;}
}
@media screen and (max-width: 2000px){
